@import '../../styles/variables';

.pe-analytics-summary-tooltip {
  min-width: 140px;
  border: 1px solid $color-neutral-5;
  background: #fff;
  border-radius: 4px;
  padding: 8px 10px;
}

.pe-analytics-line-graph-container {
  ul.recharts-default-legend {
    padding-top: 8px !important;
  }

  li.recharts-legend-item {
    margin-bottom: 0;
    cursor: pointer;

    span.recharts-legend-item-text {
      color: $color-neutral-40 !important;
      vertical-align: -1px;
    }
  }
}

.pe-bar-graph-container {
  ul.recharts-default-legend {
    padding-top: 8px !important;
  }

  li.recharts-legend-item {
    margin-bottom: 0;

    span.recharts-legend-item-text {
      color: $color-neutral-40 !important;
      vertical-align: -1px;
    }
  }
}

.pe-subscription-rate-input-number {
  width: 56px !important;

  .pe-ant-input-number-input-wrap > .pe-ant-input-number-input {
    border: unset;
  }
}
