#pe-main-wrapper,
[class^='pe-ant-'],
[class*='pe-ant-'] {
  font-family: 'SF Pro Display', sf-pro-display, -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif !important;
  -webkit-font-smoothing: antialiased;
}

/* stylelint-disable-next-line selector-class-pattern */
body.toplevel_page_pushengage {
  #wpcontent {
    padding-left: 0;
  }
}

.pushengage-app {
  font-size: 14px;
}

.pe-app-layout {
  min-height: 100vh;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  min-height: calc(100vh - 100px);
}

// Page and section container Containers
.pe-page-container,
.pe-container {
  @include screen(tablet) {
    padding: 24px 20px;
  }
  @include screen(mobile) {
    padding: 24px 16px;
  }

  padding: 50px 40px;
}

.pe-page-container {
  overflow-x: auto;
  position: relative;
}

.pe-page,
.pe-content {
  margin: auto;
  width: 100%;
}

.pe-page-blocked {
  min-height: 750px;
  @include screen(tablet) {
    min-height: 850px;
  }
  @include screen(mobile) {
    min-height: 1024px;
  }
}

.pe-section {
  padding: 32px;
  background-color: $color-white;
  border-radius: 4px;
  box-shadow: 0 3px 2px rgb(0 0 0 / 5%);

  @include screen(tablet) {
    padding: 24px;
  }
  @include screen(mobile) {
    padding: 16px;
  }
}

.pe-ellipsis {
  width: 100%;
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.pe-box-secondary {
  border: 1px solid $color-neutral-15;
  padding: 24px 24px 20px;
  border-radius: 5px;
}

.pe-box-primary {
  border: 1px solid $color-accent-lighter;
  padding: 24px 24px 20px;
  border-radius: 5px;
  box-shadow: 0 0 0 3px rgb(59 67 255 / 10%);
}

.pe-section-blur {
  overflow: hidden;
  clear: both;
  pointer-events: none;
  user-select: none;
  opacity: 0.3;
}
