@import '../designMixin';

.pe-ant-card.opt-in__chrome-native-desktop {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.05);
  width: 340px;
  position: absolute;
  left: 0;
  top: 0;

  &.opt-in__chrome-native-desktop--desktop_test_view {
    top: 50px;
  }

  .pe-ant-card-body {
    padding: 6px 10px 15px;
  }

  .close-icon-preview {
    position: absolute;
    right: 12px;
  }

  .title-preview {
    margin-top: 14px;

    .pe-ant-typography {
      @include text-ellipsis;

      font-size: 16px;
      margin-bottom: 5px;
    }
  }

  .message-preview {
    .pe-ant-typography {
      font-size: 15px;
      opacity: 0.8;
    }
  }

  .action-preview {
    .pe-ant-btn {
      margin: 0 5px;
      color: #1890ff;
      padding: 4.3px 15px;
    }
  }
}
