@import '../../styles/mixins';
@import '../../styles/variables';

$color-bg: #f5f5f8;
$card-color: #23282d;

.pe-onboarding {
  &-container {
    background: $color-bg;
    bottom: 0;
    left: 0;
    margin: 0 auto;
    overflow: auto;
    padding: 70px 16px;
    position: fixed;
    right: 0;
    top: 0;

    // In the onboarding final page, there are some modal which has z-index 100002.
    // Before modifying this z-index, please make sure that the modal z-index is higher than this.
    z-index: 100001;

    .pe-onboarding-panel {
      margin: 0 auto;
      max-width: 900px;

      &-header {
        margin: 0 auto 40px;
        text-align: center;

        img {
          width: 350px;
          @include screen(mobile) {
            width: 250px;
          }
        }
      }

      .pe-onboarding-card {
        max-width: 900px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        background-color: $color-white;
        box-shadow: 0 3px 2px rgb(0 0 0 / 5%);
        margin-bottom: 16px;
        position: relative;
        flex: 0 1 100%;
        color: $card-color;
        flex-direction: column;

        &-content {
          padding: 80px 0;
          width: 530px;
          margin: auto;
          max-width: 100%;
          @include screen(tablet) {
            padding-left: 30px;
            padding-right: 30px;
          }
          @include screen(mobile) {
            padding-left: 10px;
            padding-right: 10px;
          }

          &.small {
            width: 620px;
          }
        }
      }

      h1.step-title {
        font-size: 24px;
        line-height: 130%;
        margin-top: 16px;
        margin-bottom: 16px;
      }

      .pe-onboarding-wizard-exit {
        margin-top: 40px;
      }

      .pe-onboarding-action-buttons {
        padding-left: 45px;
        padding-right: 45px;
        margin-top: 35px;
        @include screen(mobile) {
          padding-left: 10px;
          padding-right: 10px;
        }
        button {
          font-size: 21px;
          overflow-wrap: break-word;
          word-wrap: break-word;
          @include screen(tablet) {
            font-size: 18px;
          }
          @include screen(mobile) {
            font-size: 15px;
          }
        }
      }

      .pe-onboarding-footer {
        padding: 40px 30px;
        display: flex;
        justify-content: space-between;
        border-top: 1px solid #e8e8eb;
      }
    }
  }
}
