@import './variables';
@import './mixins';

.notification {
  &__preview {
    box-shadow: 0 0 1px 0 rgb(0 0 0 / 10%), 0 1px 1px 0 rgb(0 0 0 / 20%);
    min-height: 65px;
    max-width: 360px;
    position: relative;
    width: 100%;
  }
}

.disabled-segments {
  cursor: not-allowed;
  opacity: 0.5;
  background-color: #eee !important;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgb(255 255 255 / 40%);
    z-index: 1;
  }
}

.page__notifications #multi-actions {
  margin: 8px 0;
}

.page__notifications {
  #android-options,
  #ios-options,
  #app-push-common-options {
    margin-top: 8px;

    .form-label-color {
      color: rgb(0 0 0 / 85%);
    }
  }
}

.app-push-advanced-settings {
  border-radius: 4px !important;

  .ant-card-head {
    .ant-card-head-title {
      padding: 8px 0;
    }
  }
}

.app-push-android-options {
  border-radius: 4px !important;

  .ant-collapse-header {
    padding: 8px 16px !important;
    font-weight: 600;
  }
}

.ios-push-android-options {
  border-radius: 4px !important;

  .ant-collapse-header {
    padding: 8px 16px !important;
    font-weight: 600;
  }
}

.app-push-common-title {
  font-weight: 600;
}

#general-info,
#multi-actions,
.welcome-notification,
.default-notification {
  .ant-form-extra {
    text-align: right;
  }
}

.notification-overview {
  margin-top: 1em;

  p {
    margin-bottom: 0 !important;
    padding-bottom: 1em;
  }
}

#create-form-ab .notification-a .notification__ab-title {
  @media screen and (max-width: 991px) {
    text-align: left !important;
  }
}

#utm-params {
  .ant-collapse-header {
    cursor: default !important;
  }

  .ant-collapse-item {
    .ant-collapse-extra {
      position: absolute;
      top: 16px;
      right: 16px;
      float: none;
    }
  }
}

.notification-a,
.notification-b {
  &--collapse {
    background-color: transparent !important;

    & > .ant-collapse-item {
      & > .ant-collapse-header {
        cursor: default !important;

        & > .ant-collapse-extra {
          position: absolute;
          top: 16px;
          right: 16px;
          float: none;
          display: none;
          @media screen and (max-width: 991px) {
            display: block;
          }
        }
      }

      & > .ant-collapse-content > .ant-collapse-content-box {
        padding: 0 !important;
      }
    }
  }
}

.allow-notification-modal {
  width: 500px !important;
}

#android-action-buttons,
#ios-action-buttons {
  .remove-action-button {
    .ant-btn {
      border: unset;
      box-shadow: unset;

      &:hover {
        color: $color-red;
      }
    }
  }
}

#mobile-push-additions-fields {
  .remove-additional-field {
    .ant-btn {
      border: unset;
      box-shadow: unset;

      &:hover {
        color: $color-red;
      }
    }
  }
}

.overlayPreview {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;

  &__dialog {
    background-color: #fff;
    width: 350px;
    margin-left: 7.5%;
    padding: 18px;
    font-family: sans-serif !important;
    position: relative;
    overflow: hidden;
    box-shadow: 0 0 4px 0 rgb(0 0 0 / 50%);
    border-radius: 3px;
  }

  &__close {
    position: absolute;
    right: 15px;
    line-height: 24px;
    font-size: 24px;
    top: 5px;
  }

  &__btn {
    border: thin solid rgb(0 0 0 / 20%);

    // box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.3);
    padding: 5px 16px;
    margin-right: 8px;
    float: right;
    border-radius: 3px;
    transition: all 0.2s ease;
    cursor: default;

    &:hover {
      background-color: #fafafa;
      transition: all 0.2s ease;
    }
  }

  &__banner {
    width: 100%;
    min-height: 30px;
    background-color: #fff;
    bottom: 0;
    left: 0;
    position: absolute;
    padding: 10px 20px 25px;
  }

  &__branding {
    position: fixed;
    bottom: 0;
    right: 0;
    text-decoration: none;
    color: #fff;
    background-color: transparent;
    background-image: url('https://assetscdn.pushengage.com/site_assets/img/poweredby/poweredby.png');
    width: 184px;
    height: 26px;
    background-repeat: no-repeat;
    z-index: 1;
    border-radius: 4px 0 0;
  }

  &__overlayText {
    left: 0;
    right: 0;
    position: absolute;
    width: 260px;
    box-shadow: 0 2px 2px 0 rgb(0 0 0 / 14%), 0 3px 1px -2px rgb(0 0 0 / 12%),
      0 1px 5px 0 rgb(0 0 0 / 20%);
    background: #fff;
    color: #000;
    border-radius: 5px;
    margin: 0 auto;
    top: 185px;

    img {
      margin: 0;
      padding: 0;
      width: 100%;
      border-radius: 5px 5px 0 0;
    }

    p {
      margin: 8px 10px;
      padding: 0;
      line-height: 22px;
      max-height: 400px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &--icon {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 550px;
    top: 135px;
    transform: rotate(-7deg);
  }
}

.SMPreview {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;

  &__overlay {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    background-color: rgb(0 0 0 / 50%);
  }

  &__widget {
    padding: 0 10px;
    line-height: 35px;
  }

  &__bell {
    width: 50px;
    height: 50px;
    background-color: $color-white;
    border-radius: 50%;
    margin: 50px 30px;
    padding: 5px;
  }

  &__tooltip {
    .ant-tooltip-inner {
      background-color: #000 !important;
      padding: 9px !important;
    }

    .ant-tooltip-arrow {
      display: none;
    }

    .ant-tooltip-content {
      display: inline-block !important;
      width: auto !important;
      white-space: nowrap !important;
    }

    &.end {
      .ant-tooltip-content {
        margin-left: -20px !important;
      }
    }
  }

  &__blockbtn {
    font-size: 12px;
    line-height: 22.1px;
    letter-spacing: 0.65px;
    text-align: center;
    cursor: pointer;
    margin: 8px 0;
    padding: 5px 10px;
    border-radius: 4px;
  }
}

.preview__mobile--wrap {
  .preview__8 .preview__branding {
    order: 4;
  }
}

.pushengage-optin-reminder {
  &--overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    background: rgb(0 0 0 / 50%);
  }

  &--preview {
    position: fixed;
    width: 260px;
    box-shadow: 0 2px 2px 0 rgb(0 0 0 / 14%), 0 3px 1px -2px rgb(0 0 0 / 12%),
      0 1px 5px 0 rgb(0 0 0 / 20%);
    border-radius: 5px;

    &.left {
      left: 100px;
      top: 135px;
    }

    &.right {
      right: 100px;
      top: 135px;
    }

    &.bottom_left {
      left: 100px;
      bottom: 0;
    }

    &.bottom_right {
      right: 100px;
      bottom: 0;
    }

    &.center {
      margin: 0 auto;
      top: 135px;
      left: 0;
      right: 0;
    }

    img {
      width: 100%;
      border-radius: 5px 5px 0 0;
      margin: 0;
      padding: 0;
    }

    p {
      margin: 8px 10px;
      padding: 0;
      line-height: 22px;
      max-height: 350px;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .close {
      max-width: 200px;
      max-height: 30px;
      float: right;
      padding: 0 7px;
      min-width: 60px;
      text-align: center;
      border-radius: 2px;
      cursor: pointer;
      font-size: 15px;
      overflow: hidden;
      text-overflow: ellipsis;
      line-height: 30px;
      box-shadow: 0 1px 3px 0 rgb(0 0 0 / 20%), 0 1px 1px 0 rgb(0 0 0 / 14%),
        0 2px 1px -1px rgb(0 0 0 / 12%);
      margin: 0 8px 12px 0;
    }
  }
}

.pe-collapsible-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s;
}

.pe-collapsible-expanded {
  max-height: 100%;
  transition: max-height 0.2s;
}
