@mixin opt-in-button-paragraph {
  font-size: 12px;
  font-weight: 600;
  line-height: 15px;
  text-align: center;
  word-break: break-word;
  font-style: normal;
}

@mixin opt-in-action-button {
  align-items: center;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  padding: 10px 24px;
  @include opt-in-button-paragraph;

  p {
    @include opt-in-button-paragraph;
  }

  &:hover {
    opacity: 0.8;
  }
}

@mixin opt-in-paragraph {
  word-wrap: break-word;
  overflow: hidden;
  font-size: 13px;
  font-weight: 500;
  font-style: normal;
  text-align: left;
}

@mixin opt-in-title {
  @include opt-in-paragraph;

  p {
    @include opt-in-paragraph;
  }

  .ql-size-large {
    font-size: 1.5em;
    line-height: 24px;
  }

  .ql-size-small {
    font-size: 0.75em;
    line-height: 18px;
  }

  ol,
  ul {
    text-align: left;
    margin: 0 0 5px;
    padding: 0 0 0 1.5em;
    list-style-image: none;

    li {
      @include opt-in-paragraph;

      overflow: inherit;
      margin: 0;
      padding: 0;
    }
  }

  ol {
    list-style-type: decimal;
  }

  ul {
    list-style-type: disc;
  }
}

@mixin reset-opt-in-paragraph-style {
  /*
  This sets all properties to "unset," but it is not supported in all browsers
  due to the added margin and padding.
  */
  all: unset;
  font-family: inherit;
  margin: 0;
  padding: 0;

  span {
    all: unset;
    font-family: inherit;
    margin: 0;
    padding: 0;
  }
}

@mixin reset-opt-in-style {
  all: unset;
  letter-spacing: normal;
  word-spacing: normal;
  text-shadow: none;
  font-family: inherit;
  box-sizing: border-box;
  border: 0;
  vertical-align: initial;
  background-repeat: no-repeat;
  font-weight: normal;
  font-style: normal;
  line-height: normal;
  text-transform: initial;
  border-width: 0;
  border-style: none;
  padding: 0;
  margin: 0;
  text-align: left;
}

@mixin opt-in-icon-container {
  width: 48px;
  height: 48px;
}

@mixin opt-in-icon-container-default-icon {
  align-items: center;
  justify-content: center;
  background-color: $opt-in-theme-color;
  border-radius: 50%;

  svg {
    fill: #fff;
    width: 24px;
    height: 24px;
  }
}

@mixin opt-in-icon {
  background-image: var(--opt-in-icon-url);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 4px;
  display: block;
  height: auto;
  width: 100%;
  max-height: 100%;
}

@mixin opt-in-powered-by {
  box-sizing: border-box;
  position: absolute;
  font-size: 8px;
  height: 9px;
  line-height: 9px;
  cursor: pointer;
  font-weight: 500;
  background: none;
  opacity: 0.8;

  span {
    text-decoration-line: underline;
    font-weight: 600;
    letter-spacing: 0.3px;
  }
}

@mixin icon-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin opt-in-mask {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.45);
}

@mixin text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@mixin opt-in-segment-body-scroll {
  max-height: 300px;
  min-height: auto;
  height: auto;
  overflow-y: auto;
}

@mixin tooltip-title {
  @include opt-in-title;

  font-weight: 700;
  line-height: 20px;

  & p {
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
  }
}

@mixin tooltip-powered-by {
  @include opt-in-powered-by;

  bottom: 3px;
  right: 22px;
}
