@import '../../styles/variables';
@import '../../styles/mixins';

.campaigns-breadcrumb-navbar {
  min-height: 56px;
  row-gap: 8px !important;

  .campaigns-breadcrumb {
    color: $color-neutral-60;
    font-weight: 500;

    .indicator {
      font-size: 15px;
      color: $color-neutral-15;
    }

    .segment {
      margin-right: 32px;
      font-size: 16px;
      transition: all 0.4s;
      display: inline-block;

      &:hover {
        color: $color-primary-40;
      }
    }

    .active {
      color: $color-primary;
      font-weight: 600;
    }
    @include screen(mobile) {
      .segment {
        margin-right: 16px;
        font-size: 14px;
      }

      .indicator {
        font-size: 12px;
      }
    }
  }

  .campaigns-breadcrumb-right {
    align-self: center;
    text-align: end;
  }

  &.has-border-bottom {
    border-bottom: 1px solid $color-neutral-15;
  }

  &.has-border-top {
    margin-top: 20px;
    border-top: 1px solid $color-neutral-15;
  }

  &.placement-top {
    padding-bottom: 16px;
  }

  &.placement-bottom {
    padding-top: 16px;
  }
}
