@use 'sass:color';
@import './variables';

// Export button
button.pe-ant-btn-export,
span.pe-ant-btn-export button {
  padding: 0;
  font-weight: 600;
  font-size: 13px;
  margin-left: 8px;

  &.pe-ant-btn-loading::before {
    background-color: transparent;
  }
}

// underline text button
button.pe-ant-btn-text {
  span.underline-text {
    border-bottom: solid 1px $color-neutral-6;
    line-height: 110%;
    font-weight: 500;
    margin-left: 5px;
  }
}

button.pe-ant-btn-green {
  color: #fff;
  background: $color-green;
  border-color: $color-green-darker;

  &:hover,
  &:active,
  &:focus {
    background-color: $color-green-darker;
    border-color: $color-green-darker;
  }
}

// extra small text button
// Do not use it
.pe-ant-btn-xs {
  padding: 2px 20px;
  font-size: 13px;
  font-weight: 400;
  height: 30px;
}

// Do not use it .
// Use <Button type='text'><span className='underline-text'></Button>
.pe-btn-plain {
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 130%;
  text-align: center;
  color: $color-neutral-50;
  background: transparent;
  border: none;
  box-shadow: none;
  padding-left: 0;
  padding-right: 0;
  transition: all 0.4s;
  white-space: normal;

  &:hover {
    color: $color-neutral-30;
    background: transparent;
    border: none;
    box-shadow: none;
  }

  .pe-btn-txt-underline {
    border-bottom: 1px solid rgb(71 72 93 / 20%);
  }
}
