@import '../designVariables';
@import '../designMixin';
@import '../optInBellDesign';
@import '../optInTooltip';

.subscriber-recovery-widget {
  @include reset-opt-in-style;

  &.subscriber-recovery-widget--button {
    height: var(--widget-height);
    min-width: 200px;
    position: absolute;
    text-align: center;
    width: max-content;

    &.subscriber-recovery-widget--dark {
      .subscriber-recovery-widget__powered-by {
        color: $default-light-color;
      }
    }

    &.subscriber-recovery-widget--light {
      .subscriber-recovery-widget__powered-by {
        color: $default-dark-color;
      }
    }

    &.subscriber-recovery-widget--bottom {
      @mixin widget-bottom {
        .subscriber-recovery-widget__body {
          border-radius: 8px 0 0;
          margin: 10px 10px 0 0;
        }

        .subscriber-recovery-widget__close-icon {
          top: 0;
          right: 0;
        }

        .subscriber-recovery-widget__powered-by {
          right: 34px;
        }

        &.subscriber-recovery-widget--mobile_test_view {
          bottom: 14px;
        }
      }

      &-right {
        bottom: 0;
        right: 0;

        @include widget-bottom;

        &.subscriber-recovery-widget--mobile_test_view {
          right: 14px;
        }
      }

      &-left {
        bottom: 0;
        left: 0;

        @include widget-bottom;

        &.subscriber-recovery-widget--mobile_test_view {
          left: 13px;
        }
      }

      &-center {
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 0);

        @include widget-bottom;
      }
    }

    &.subscriber-recovery-widget--right {
      @mixin widget-right {
        .subscriber-recovery-widget__body {
          border-radius: 8px 0 0;
          margin: 10px 10px 0 0;
        }

        .subscriber-recovery-widget__close-icon {
          right: 0;
          top: 0;
        }

        .subscriber-recovery-widget__powered-by {
          right: 34px;
        }

        &.subscriber-recovery-widget--mobile_test_view {
          right: 14px;
        }
      }

      &-top {
        top: calc(var(--widget-width) + 50px);
        right: 0;
        transform-origin: top left;
        transform: rotate(270deg) translateY(calc(var(--widget-width) - var(--widget-height)));

        @include widget-right;
      }

      &-bottom {
        bottom: var(--widget-width);
        right: 0;
        transform-origin: bottom right;
        transform: rotate(270deg) translateY(0px);

        @include widget-right;

        &.subscriber-recovery-widget--mobile_test_view {
          bottom: calc(var(--widget-width) + 14px);

          .subscriber-recovery-widget__body {
            border-bottom-left-radius: 20px;
          }
        }
      }

      &-center {
        bottom: calc(50% + var(--widget-width) / 2);
        right: 0;
        transform-origin: bottom right;
        transform: rotate(270deg) translateY(0);

        @include widget-right;
      }
    }

    &.subscriber-recovery-widget--left {
      @mixin widget-right {
        .subscriber-recovery-widget__body {
          border-radius: 0 0 0 8px;
          margin: 0 10px 10px 0;
        }

        .subscriber-recovery-widget__close-icon {
          right: 0;
          bottom: 0;
        }

        .subscriber-recovery-widget__powered-by {
          right: 34px;
          top: 2.5px;
          bottom: unset;
        }

        &.subscriber-recovery-widget--mobile_test_view {
          left: 14px;
        }
      }

      &-top {
        top: calc(var(--widget-width) - var(--widget-height) + 50px);
        left: 0;
        transform-origin: bottom left;
        transform: rotate(270deg) translateY(var(--widget-height));

        @include widget-right;
      }

      &-bottom {
        bottom: 0;
        left: 0;
        transform-origin: bottom left;
        transform: rotate(270deg) translateY(var(--widget-height));

        @include widget-right;

        &.subscriber-recovery-widget--mobile_test_view {
          bottom: 14px;

          .subscriber-recovery-widget__body {
            border-top-left-radius: 20px;
          }
        }
      }

      &-center {
        bottom: calc(50% - var(--widget-width) / 2);
        left: 0;
        transform-origin: bottom left;
        transform: rotate(270deg) translateY(var(--widget-height));

        @include widget-right;
      }
    }

    .subscriber-recovery-widget__body {
      padding: 14px 24px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      font-size: 13px;
      font-weight: 600;
      line-height: 18px;
      background-color: var(--widget-background-color);
      color: var(--widget-color);
      box-shadow: 0 -2px 4px rgba(121, 130, 154, 0.1);

      .subscriber-recovery-widget__title {
        padding-left: 10px;
        cursor: pointer;
      }

      .subscriber-recovery-widget__bell-icon {
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
          fill: var(--widget-color);
          width: 14px;
          height: 14px;
        }
      }

      .subscriber-recovery-widget__powered-by {
        @include opt-in-powered-by;

        bottom: 2.5px;
        line-height: 9px;
      }
    }

    .subscriber-recovery-widget__close-icon {
      position: absolute;
      box-sizing: border-box;
      cursor: pointer;
      border-radius: 20px;
      background: var(--widget-color);
      box-shadow: 0 4px 6px 0 rgba(121, 130, 154, 0.1);
      backdrop-filter: blur(2px);
      width: 20px;
      height: 20px;
      display: flex;
      align-items: center;
      justify-content: center;

      svg {
        fill: var(--widget-background-color);
        width: 10px;
        height: 10px;

        &:hover {
          opacity: 0.7;
        }
      }
    }
  }
}

.subscriber-recovery-widget__tooltip {
  .subscriber-recovery-widget__tooltip__title {
    @include tooltip-title;
  }

  .subscriber-recovery-widget__tooltip__powered-by {
    @include tooltip-powered-by;
  }
}
