@import '../../../styles/variables';

.picker__wrap {
  .pe-ant-popover-inner-content {
    padding: 0 0 10px;
  }

  aside.emoji-picker-react {
    border: none;
    box-shadow: none;

    .active-category-indicator-wrapper {
      .active-category-indicator {
        background-color: $color-primary;
      }
    }

    .emoji-group::before {
      font-size: 11px;
      color: $color-neutral-50;
    }
  }
}

.pe-ant-input-group-wrapper,
.pe-ant-input-affix-wrapper {
  span.pe-ant-input-suffix {
    color: $color-neutral-40;
    font-weight: 400;
  }

  &.goal-length-exceeded {
    span.pe-ant-input-suffix {
      color: $color-red;
    }
  }
}

.pe-notification-title,
.pe-notification-msg,
.pe-notification-url {
  .pe-ant-input-affix-wrapper {
    border-color: $color-neutral-40;
    font-weight: 500;

    input.pe-ant-input {
      color: $color-neutral-70;
      font-size: 16px;
    }
  }

  .pe-ant-input-group-addon {
    border-color: $color-neutral-40;
    color: $color-neutral-70;
  }
}
