@import '../../styles/campaign';
@import '../../styles/variables';

.pe-ab-noti-tab {
  .pe-ant-tabs-nav {
    margin-bottom: 0;
  }

  .pe-ant-tabs-tab + .pe-ant-tabs-tab {
    margin: 0;
  }

  .pe-ant-tabs-tab {
    color: $color-neutral;
    background-color: $color-neutral-10;
    padding: 12px 20px !important;
    font-size: 16px;

    &:hover {
      color: $color-neutral;
    }
  }

  .pe-ant-tabs-tab.pe-ant-tabs-tab-active .pe-ant-tabs-tab-btn {
    color: $color-neutral;
    font-weight: 600;
    text-shadow: none;
  }

  .pe-ant-tabs-tab-active {
    background: $color-white;
    border-color: $color-white;
    border-bottom: none;
    border-bottom-color: $color-white;
  }

  .pe-ant-tabs-ink-bar {
    background-color: $color-white;
  }

  &.pe-ant-tabs-top {
    .pe-ant-tabs-nav {
      &::before {
        border-bottom: none;
      }
    }
  }
}
