.opt-in__bell {
  @include reset-opt-in-style;

  backdrop-filter: blur(2px);
  bottom: var(--bell-bottom);
  box-shadow: 0px 4px 6px rgba(121, 130, 154, 0.1);
  display: block;
  position: absolute;
  visibility: visible !important;
  border-radius: 50%;
  background-color: var(--bell-background-color);

  &.opt-in__bell--right {
    right: var(--bell-right);
  }

  &.opt-in__bell--left {
    left: var(--bell-left);
  }

  .opt-in__bell__icon {
    @include icon-center;

    cursor: pointer;
    height: var(--bell-height);
    width: var(--bell-width);

    svg {
      width: 18px;
      height: 22px;
      fill: var(--bell-color);
    }
  }
}
