.opt-in__sleek-opt-in-box {
  $opt-in-x-padding: 16px;

  @include reset-opt-in-style;

  background-color: var(--opt-in-background-color);
  margin: 0;
  padding: 12px $opt-in-x-padding;
  position: absolute;
  visibility: visible !important;
  width: var(--opt-in-width);
  display: flex;
  flex-flow: column nowrap;
  max-height: 100%;
  overflow-y: hidden;

  &.opt-in__sleek-opt-in-box--light {
    color: $default-dark-color;
  }

  &.opt-in__sleek-opt-in-box--dark {
    color: $default-light-color;
  }

  &.opt-in__sleek-opt-in-box--pe-top-left {
    border-radius: 0 0 12px 12px;
    box-shadow: 2px 2px 5px $box-shadow-color;
    left: 0;
    top: 0;

    &.opt-in__sleek-opt-in-box--desktop_test_view {
      top: 50px;
    }
  }

  &.opt-in__sleek-opt-in-box--pe-top-right {
    border-radius: 0 0 12px 12px;
    box-shadow: -2px 2px 5px $box-shadow-color;
    right: 0;
    top: 0;

    &.opt-in__sleek-opt-in-box--desktop_test_view {
      top: 50px;
    }
  }

  &.opt-in__sleek-opt-in-box--pe-top-center {
    border-radius: 0 0 12px 12px;
    box-shadow: 2px 2px 5px $box-shadow-color;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0);

    &.opt-in__sleek-opt-in-box--desktop_test_view {
      top: 50px;
    }
  }

  &.opt-in__sleek-opt-in-box--pe-mobile-bottom {
    border-radius: 12px 12px 0 0;
    bottom: 0;
    box-shadow: 2px -2px 5px $box-shadow-color;
    left: 50%;
    transform: translate(-50%, 0);

    &.opt-in__sleek-opt-in-box--mobile_test_view {
      bottom: 14px;
    }
  }

  &.opt-in__sleek-opt-in-box--pe-mobile-top {
    border-radius: 0 0 12px 12px;
    box-shadow: 2px 2px 5px $box-shadow-color;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);

    &.opt-in__sleek-opt-in-box--mobile_test_view {
      top: 41px;
    }
  }

  .opt-in__sleek-opt-in-box__body {
    display: flex;
    flex-flow: column nowrap;

    /*
    The header styling here differs from that in the web SDK because it uses a
    100% preview width, which disrupts the ellipsis of the text due to the flex
    display type. Now, we're using a block display type combined with the float
    property.
    */
    .opt-in__sleek-opt-in-box__header {
      display: block;
      width: 100%;

      .opt-in__sleek-opt-in-box__header__title {
        display: block;
        width: calc(100% - 26px);
        float: left;
        margin-right: 15px;

        .opt-in__sleek-opt-in-box__bell-icon {
          @include icon-center;

          float: left;
          width: 17px;
          line-height: 11px;

          svg {
            fill: var(--allow-btn-background-color);
            width: 12px;
            height: 12px;
          }
        }

        .opt-in__sleek-opt-in-box__site-name {
          @mixin ellipsis-site-name {
            @include text-ellipsis;

            display: block;
          }

          @include ellipsis-site-name;

          font-style: normal;
          font-weight: 500;
          font-size: 11px;
          line-height: 11px;
          text-transform: uppercase;

          & * {
            @include ellipsis-site-name;
          }
        }
      }

      .opt-in__sleek-opt-in-box__close-icon {
        @include icon-center;

        cursor: pointer;
        display: block;
        line-height: 11px;

        &:hover {
          opacity: 0.7;
        }

        svg {
          fill: var(--close-btn-background-color);
          width: 11px;
          height: 11px;
        }
      }
    }

    .opt-in__sleek-opt-in-box__content {
      display: flex;
      justify-content: space-between;
      flex-flow: row nowrap;
      margin-top: 19px;

      .opt-in__sleek-opt-in-box__title {
        @include opt-in-title;

        display: flex;
        line-height: 18px;
        margin-right: 18px;
        flex-grow: 1;

        p {
          line-height: 18px;
        }
      }

      .opt-in__sleek-opt-in-box__icon__container {
        @include opt-in-icon-container;

        display: flex;
        flex-shrink: 0;

        &.opt-in__sleek-opt-in-box__icon__container--default-icon {
          @include opt-in-icon-container-default-icon;
        }

        .opt-in__sleek-opt-in-box__icon {
          @include opt-in-icon;
        }
      }
    }

    .opt-in__sleek-opt-in-box__segments {
      @include opt-in-segment-body-scroll;

      display: flex;
      flex-wrap: wrap;
      margin-top: 8px;
      margin-right: -$opt-in-x-padding;
      padding-right: $opt-in-x-padding;

      .opt-in__checkbox {
        flex-basis: calc(50% - 8px);

        &:nth-child(even) {
          margin-left: 16px;
        }
      }
    }

    .opt-in__sleek-opt-in-box__button {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      margin-top: 11px;

      .opt-in__sleek-opt-in-box__button__allow {
        @include opt-in-action-button;

        background: var(--allow-btn-background-color);
        color: #fff;
        padding-top: 8px;
        padding-bottom: 8px;
      }
    }

    p {
      @include reset-opt-in-paragraph-style;
    }

    .opt-in__sleek-opt-in-box__powered-by {
      @include opt-in-powered-by;

      right: 16px;
      bottom: 3px;
    }
  }
}
