@import '../../../styles/variables';

tr.pe-ant-table-expanded-row > td {
  padding: 0 !important;
  background: $color-white !important;
}

// NOTE: please make sure the table columns width % is in synch with
// the width givin in columns config in the file PushTable.css
.pe-ab-notification-details {
  width: 100%;
  display: flex;

  .pe-notification {
    &-title {
      width: 25%;
    }

    &-stats {
      width: 17%;
    }

    &-goal {
      width: 12%;
    }

    &-ab {
      width: 11%;
    }

    &-status {
      width: 12%;
    }

    &-actions {
      width: 23%;
    }
  }
}

.pe-ab-notification-divider {
  .pe-ant-divider-inner-text {
    background-color: $color-white !important;
  }

  .pe-ab-toggle-btn {
    height: 22px !important;
    font-size: 12px !important;
    padding: 0 11px !important;
  }
}
