// Media Query
$mobile-width: 320px;
$tablet-width: 768px;
$desktop-width: 1024px;

@mixin screen($breakpoint) {
  @if $breakpoint==tablet {
    @media only screen and (max-width: 600px) {
      @content;
    }
  }

  @if $breakpoint==mobile {
    @media only screen and (max-width: 480px) {
      @content;
    }
  }
}

@mixin mobile-and-tablet {
  @media (min-width: #{$mobile-width}) and (max-width: #{$desktop-width - 1px}) {
    @content;
  }
}

@mixin replace-form-feedback-icon($img) {
  svg {
    display: none;
  }

  &::before {
    display: inline-block;
    width: 1em;
    height: 1em;
    content: ' ';
    background: $color-transparent url($img) center / 1em 1em no-repeat;
  }
}

@mixin use-before-icon($img) {
  svg {
    display: none;
  }

  &::before {
    display: inline-block;
    width: 1em;
    height: 1em;
    content: ' ';
    mask: url($img) no-repeat 50% 50%;
  }
}
