@import '../../styles/variables';
@import '../../styles/mixins';

.pe-pro-overlay {
  &-background {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    background: rgb(221 221 221 / 30%);
    backdrop-filter: blur(4px);
    padding: 48px 32px;
  }

  &-container {
    max-width: 100%;
    width: 888px;
    margin: auto;
    margin-top: 100px;
    padding: 56px 32px;
    position: relative;
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0 3px 2px rgb(0 0 0 / 5%);
    @include screen(tablet) {
      padding: 48px 32px;
    }
    @include screen(mobile) {
      padding: 48px 24px;
    }
  }

  &-close-btn {
    color: $color-neutral-50;
    position: absolute;
    top: 0;
    right: 0;
    width: 32px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-items: center;
    font-size: 16px;
    cursor: pointer;

    &:hover {
      color: $color-neutral-65;
    }
  }

  &-content {
    width: 688px;
    max-width: 100%;
    margin: auto;
    position: relative;
    flex-direction: row;
    justify-content: center;
  }
}

.pe-pro-modal {
  max-width: 100%;

  &-content {
    width: 688px;
    max-width: 100%;
    margin: auto;
    position: relative;
    flex-direction: row;
    justify-content: center;
  }

  .pe-ant-modal-body {
    padding: 56px 32px;
    margin-bottom: 64px;
    @include screen(tablet) {
      padding: 48px 32px;
    }
    @include screen(mobile) {
      padding: 48px 24px;
    }
  }
}
