@import '../../../styles/variables';

.pe-data-table {
  .pe-ant-table-thead {
    & > tr > th {
      &.pe-ant-table-cell {
        font-size: 14px;
        padding-top: 13px;
        padding-bottom: 12px;
        border-top: solid 1px $color-neutral-5;
        border-bottom: solid 1px $color-neutral-5;
      }
    }
  }

  .pe-ant-table-summary {
    & > tr > td {
      &.pe-ant-table-cell {
        font-size: 14px;
        padding-top: 13px;
        padding-bottom: 12px;
        border-top: solid 1px $color-neutral-5;
        border-bottom: solid 1px $color-neutral-5;
      }
    }
  }

  .pe-ant-table-tbody > tr > td {
    &.pe-ant-table-cell {
      &:first-child {
        padding-left: 18px;
      }
    }
  }

  .pe-ant-table-tbody > tr.pe-ant-table-row:hover > td,
  .pe-ant-table-tbody > tr > td.pe-ant-table-cell-row-hover {
    background: #fafaff;
  }

  .pe-ant-table-cell {
    font-weight: 400 !important;
    line-height: 150%;

    &::before {
      content: none !important;
    }
  }

  tbody tr:nth-child(even) td {
    background-color: $color-neutral-3;
  }

  tbody tr:nth-child(odd) td {
    background-color: #fff;
  }

  tbody tr {
    vertical-align: top;
  }

  .pe-table-extended-row {
    display: none !important;
  }

  .pe-ant-table-expanded-row {
    .pe-ant-table-cell {
      background-color: $color-white !important;
    }
  }
}
