@import '../../styles/mixins';
@import '../../styles/variables';

.pe-cleanup-subscribers-modal {
  .pe-ant-card {
    box-shadow: none;
  }

  .pe-cleanup-subscriber-card {
    border-radius: 0;
    min-height: 120px;
  }

  .pe-cleanup-subscriber-card-left {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 40px;

    .pe-ant-card-body {
      padding: 0 25px 0 0;
    }
    @include screen(mobile) {
      .pe-ant-card-body {
        padding: 0;
      }
    }
  }

  .pe-cleanup-subscriber-card-right {
    width: 100%;
    border-left: 1px solid $color-neutral-5;
  }
  @include screen(tablet) {
    .pe-cleanup-subscriber-card-right {
      border-left: none;
    }
  }
}
