.opt-in__checkbox {
  @include reset-opt-in-style;

  display: inline-flex;
  align-items: flex-start;
  justify-content: center;
  box-sizing: border-box;

  &.opt-in__checkbox--small {
    input[type='checkbox'] {
      width: 14px;
      height: 14px;
      min-height: 14px;
      min-width: 14px;
      max-width: 14px;
      max-height: 14px;
      margin: 2px 0 0;
      border-radius: 3.6px;

      &::before {
        top: 1.5px;
        left: 3.5px;
        width: 5.2px;
        height: 8.2px;
      }
    }

    label {
      font-size: 11px;
    }
  }

  input[type='checkbox'] {
    position: relative;
    width: 16px;
    height: 16.3px;
    min-height: 16.3px;
    min-width: 16px;
    max-width: 16px;
    max-height: 16.3px;
    display: inline-block;
    color: #363839;
    border: 1px solid var(--checkbox-unchecked--color);
    border-radius: 4px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    appearance: none;
    outline: 0;
    cursor: pointer;
    transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
    margin: 1px 0 0;
    padding: 0;

    &:checked {
      border-color: var(--checkbox-checked-tick-color);
      background-color: var(--checkbox-checked-background-color);

      &::before {
        opacity: 1;
      }
    }

    &::before {
      box-sizing: border-box;
      position: absolute;
      content: '';
      display: block;
      top: 1px;
      left: 4.5px;
      width: 5px;
      height: 10px;
      border-style: solid;
      border-color: var(--checkbox-checked-tick-color);
      border-width: 0 2px 2px 0;
      transform: rotate(45deg);
      opacity: 0;
    }
  }

  label {
    position: relative;
    cursor: pointer;
    padding-left: 7px;
    margin: 0;
    display: inline-block;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: calc(100% - 16px);
    word-break: break-word;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
  }
}
