.pe-dashboard-stats-cards {
    .pe-card-xs {
        border-radius: 0;
        box-shadow: none;
        border: 1px solid #d6e2ed;
        margin: -1px;
    }
}

#pe-dashboard-widget {
    .inside {
        padding: 0;
        margin: 0;
    }

    .site-collapse-custom-collapse {
        .site-collapse-custom-panel {
            overflow: hidden;
            background: #f7f7f7;
            border: none;
            border-radius: 2px;

            .pe-ant-collapse-header {
                padding: 10px;
                border-bottom: 1px solid #eee;

                .pe-ant-collapse-header-text {
                    font-weight: 500;
                }

                .pe-ant-collapse-arrow {
                    right: 10px;
                }
            }

            .pe-ant-collapse-content-box {
                padding: 20px 14px;
                background: #fff;
            }
        }
    }
}