@import '../../../styles/variables';
@import '../../../styles/mixins';

.pe-notification-generator {
  margin-top: 4px;

  .left-section {
    padding: 32px;
    border-right: 4px solid $color-neutral-5;
  }
  @include screen(mobile) {
    .left-section {
      border-right: 0;
    }
  }

  .right-section {
    padding: 32px;
  }
}

.pe-notification-builder-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 32px 0 0;
  margin: 0;
  list-style: none;

  .pe-notification-builder-wrap {
    position: relative;
    width: 100%;
    padding-bottom: 36px;
    padding-left: 40px;
    overflow: hidden;

    &:last-child {
      padding-bottom: 24px;
    }

    .pe-notification-report {
      padding: 18px 0 18px 10px;
      border-radius: 5px;

      .pe-stats-column {
        padding-right: 50px;
      }

      .pe-notification-actions {
        justify-self: end;
      }

      @include screen(mobile) {
        .pe-notification-border-bottom {
          padding: 10px 3px;
          border-bottom: 1px solid $color-neutral-5;
        }

        .pe-notification-title {
          text-align: center;
        }

        .pe-stats-column {
          justify-self: center;
          padding-right: 0;
          text-align: center;
        }

        .pe-notification-actions {
          justify-self: center;
        }
      }
    }

    .pe-notification-builder-index {
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      float: left;
      min-height: 100%;
      text-align: center;

      &::before {
        position: absolute;
        top: 38px;
        left: 50%;
        width: 1px;
        height: 100%;
        content: '';
        background-color: $color-neutral-20;
        transition: all 0.3s ease;
      }

      .sequence {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 38px;
        height: 38px;
        margin-bottom: 8px;
        font-size: 18px;
        font-weight: 600;
        color: $color-neutral-40;
        border: 1px solid $color-neutral-20;
        border-radius: 50%;
      }

      &.pe-no-side-border {
        &::before {
          height: 0%;
        }
      }
    }

    .pe-notification-builder-content {
      padding-top: 3px;
      margin-left: 24px;

      .pe-notification-label {
        margin-bottom: 11px;
        font-size: 16px;
        font-weight: 400;
        color: $color-neutral-80;
      }

      .pe-notification-collapsible {
        padding: 28px 32px 28px 8px;
      }
      @include screen(mobile) {
        .pe-notification-collapsible {
          padding-left: 28px;
        }
      }

      .drag-drop-icon-column {
        display: flex;
        align-items: center;
        flex: 0 0 2%;
        max-width: 2%;
      }

      .drag-drop-icon {
        visibility: hidden;
      }

      .flexbox {
        display: flex;
        flex-direction: row;
        justify-content: space-around;

        p {
          padding: 0;
          margin: 0;
        }
      }

      .icon-expanded {
        transition: transform 0.4s;
        transform: rotate(90deg);
      }

      .icon-collapsed {
        transition: transform 0.4s;
        transform: rotate(0deg);
      }
    }

    .pe-move-next-prev {
      position: absolute;
      top: 50px;
      left: 30px;
      display: flex;
      flex-direction: column;
      visibility: hidden;
      transition: visibility 0.3s;
    }

    .pe-move {
      &-next,
      &-prev {
        padding: 4px;
        border-radius: 50%;
        transition: background-color 0.3s;

        &:hover {
          cursor: pointer;
          background-color: $color-neutral-15;
        }
      }

      &-disabled {
        color: $color-neutral-15;
        pointer-events: none;
        cursor: auto;
      }
    }

    &:hover {
      .drag-drop-icon {
        visibility: visible;

        &:hover {
          cursor: pointer;
        }
      }

      .pe-move-next-prev {
        visibility: visible;
      }
    }
  }
}
