.pe-advanced-settings-page {
  overflow-x: hidden;
  .pe-custom-url-switch-form-item {
    .pe-ant-form-item-control {
      .pe-ant-form-item-control-input {
        align-items: start;
        min-height: 20px;
      }
    }
  }

  .pe-safari-cert-upload-form-item {
    .pe-add-on-btn {
      border-color: transparent;
      border-radius: 0;

      &:hover,
      &:focus,
      &:active {
        cursor: pointer;
        border-color: transparent;
      }
    }

    .pe-ant-input-group-addon {
      padding: 0;
    }

    .pe-file-input {
      opacity: 0;
      position: absolute;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 0;
      margin-left: -100px;
    }
  }
}
