@import '../designVariables';
@import '../designMixin';
@import '../optInBellDesign';
@import '../optInTooltip';
@import '../optInSwitch';

.unsubscribe-widget {
  @include reset-opt-in-style;

  &.unsubscribe-widget--bell {
    // This class is responsible for displaying the widget body.
    &.unsubscribe-widget--opened {
      .unsubscribe-widget__opt-in {
        visibility: visible;
      }
    }

    // This class is responsible for displaying the automated notification text.
    &.unsubscribe-widget--automated-notification-enabled {
      .unsubscribe-widget__opt-in {
        .opt-in__switch__label-title--enabled {
          display: block;
        }

        .opt-in__switch__label-title--disabled {
          display: none;
        }
      }
    }

    &.unsubscribe-widget--right {
      .unsubscribe-widget__opt-in {
        right: var(--bell-right);
      }
    }

    &.unsubscribe-widget--left {
      .unsubscribe-widget__opt-in {
        left: var(--bell-left);
      }
    }

    .unsubscribe-widget__opt-in {
      background: rgba(255, 255, 255, 0.96);
      box-shadow: 0px 4px 6px 0px rgba(121, 130, 154, 0.1);
      backdrop-filter: blur(2px);
      position: absolute;
      padding: 16px;
      min-width: 200px;
      max-width: 360px;
      bottom: calc(var(--bell-bottom) + var(--bell-height) + 12px);
      color: $default-dark-color;
      display: flex;
      flex-flow: column nowrap;
      border-radius: 10px;
      visibility: hidden;

      .unsubscribe-widget__title {
        @include opt-in-title;

        color: $default-dark-color;
        text-align: center;
        line-height: 18px;
        margin-bottom: 16px;
      }

      .unsubscribe-widget__button {
        border-radius: 4px;
        border: 1px solid #d8d8d8;
        color: #677489;
        text-align: center;
        font-size: 12px;
        font-weight: 600;
        padding: 7px 12px;
        cursor: pointer;

        &:hover {
          opacity: 0.7;
        }
      }

      .unsubscribe-widget__switch {
        margin-bottom: 12px;

        .opt-in__switch__label-title {
          color: #364253;
        }
      }

      .opt-in__switch__label-title--enabled {
        display: none;
      }

      .opt-in__switch__label-title--disabled {
        display: block;
      }

      .unsubscribe-widget__powered-by {
        @include opt-in-powered-by;

        bottom: 3.5px;
        right: 16px;
      }
    }
  }

  &.unsubscribe-widget--button {
    height: 56px;
    min-width: 200px;
    position: absolute;
    bottom: 0;
    text-align: center;
    width: max-content;

    &.unsubscribe-widget--right {
      right: 0;
    }

    &.unsubscribe-widget--left {
      left: 0;
    }

    &.unsubscribe-widget--dark {
      .unsubscribe-widget__powered-by {
        color: $default-light-color;
      }
    }

    &.unsubscribe-widget--light {
      .unsubscribe-widget__powered-by {
        color: $default-dark-color;
      }
    }

    &.unsubscribe-widget--mobile_test_view {
      bottom: 14px;

      &.unsubscribe-widget--right {
        right: 14px;

        .unsubscribe-widget__opt-in {
          border-bottom-right-radius: 8px;
        }
      }

      &.unsubscribe-widget--left {
        left: 14px;

        .unsubscribe-widget__opt-in {
          border-bottom-left-radius: 20px;
        }
      }
    }

    &.unsubscribe-widget--initial-state {
      .unsubscribe-widget__opt-in {
        .unsubscribe-widget__title {
          display: block;
        }

        .unsubscribe-widget__thanks-msg,
        .unsubscribe-widget__action {
          display: none;
        }
      }
    }

    &.unsubscribe-widget--action-state {
      .unsubscribe-widget__opt-in {
        .unsubscribe-widget__action {
          display: block;
        }

        .unsubscribe-widget__title,
        .unsubscribe-widget__thanks-msg {
          display: none;
        }
      }
    }

    &.unsubscribe-widget--final-state {
      .unsubscribe-widget__opt-in {
        .unsubscribe-widget__thanks-msg {
          display: block;
        }

        .unsubscribe-widget__title,
        .unsubscribe-widget__action {
          display: none;
        }
      }
    }

    .unsubscribe-widget__opt-in {
      padding: 14px 24px;
      display: flex;
      flex-direction: column;
      font-size: 13px;
      font-weight: 600;
      line-height: 18px;
      background-color: var(--widget-background-color);
      color: var(--widget-color);
      box-shadow: 0px -2px 4px rgba(121, 130, 154, 0.1);
      border-radius: 8px 0 0 0;
      margin: 10px 10px 0 0;

      .unsubscribe-widget__title {
        cursor: pointer;
      }

      .unsubscribe-widget__action {
        display: none;

        span:nth-child(2),
        span:nth-child(3) {
          text-decoration-line: underline;
          cursor: pointer;
        }

        span:nth-child(2) {
          margin: 0 8px;
        }
      }

      .unsubscribe-widget__powered-by {
        @include opt-in-powered-by;

        bottom: 2.5px;
        right: 34px;
      }
    }

    .unsubscribe-widget__close-icon {
      position: absolute;
      box-sizing: border-box;
      top: 0;
      right: 0;
      cursor: pointer;
      border-radius: 20px;
      background: var(--widget-color);
      box-shadow: 0px 4px 6px 0px rgba(121, 130, 154, 0.1);
      backdrop-filter: blur(2px);
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;

      svg {
        fill: var(--widget-background-color);
        width: 10px;
        height: 10px;

        &:hover {
          opacity: 0.7;
        }
      }
    }
  }
}

.unsubscribe-widget__tooltip {
  .unsubscribe-widget__tooltip__title {
    @include tooltip-title;
  }
}
