/* stylelint-disable declaration-no-important */
@use 'sass:color';
@import './variables';

/** Text Button */
// -------------------------------------
button.pe-ant-btn-primary {
  &:hover,
  &:active,
  &:focus {
    color: $color-white;
    background-color: $color-primary-darker;
    border-color: $color-primary-darker;
  }

  &:disabled {
    &,
    &:active,
    &:focus,
    &:hover {
      background: #f0f0f5;
      border-color: #c9c9d6;
      box-shadow: none;
      color: #9e9eab;
      text-shadow: none;
    }
  }
}

button.pe-ant-btn-default {
  // color: $color-neutral-60;

  &:hover,
  &:focus,
  &:active {
    border-color: $color-primary;
    color: $color-neutral-60;
  }

  &:disabled {
    &,
    &:active,
    &:focus,
    &:hover {
      border-color: $color-neutral-15;
    }
  }

  &.pe-ant-btn-icon-only:not(.pe-ant-btn-dangerous) {
    color: $color-neutral-40;
    border-color: $color-neutral-15;

    &:hover,
    &:focus,
    &:active {
      border-color: $color-primary;
      color: $color-primary;
    }

    &:disabled {
      &,
      &:active,
      &:focus,
      &:hover {
        color: $color-neutral-30;
        border-color: $color-neutral-15;
      }
    }
  }

  &.pe-ant-btn-dangerous {
    &:hover,
    &:focus,
    &:active {
      border-color: $color-red;
      color: $color-red;
    }

    &:disabled {
      &,
      &:active,
      &:focus,
      &:hover {
        color: $color-neutral-30;
        border-color: $color-neutral-15;
      }
    }
  }
}

button.pe-ant-btn-link {
  &:hover,
  &:focus,
  &:active {
    color: $color-primary-darker;
  }
}

button.pe-ant-btn {
  .anticon {
    font-size: 13px;
  }

  &-sm {
    .anticon {
      font-size: 11px;
    }

    &.pe-ant-btn-circle {
      .anticon {
        font-size: 13px;
      }
    }
  }

  &-lg {
    height: 40px;
    font-size: 14px;
  }

  // Pushengage secondary button
  &.pe-btn-secondary {
    background-color: $color-neutral-10;
    border: none;

    &:hover,
    &:focus {
      color: $color-neutral-60;
      background-color: $color-neutral-15;
    }

    &:active {
      color: $color-neutral-60;
      background-color: $color-neutral-10;
    }
  }

  // Pushengage hallow button
  // &.pe-btn-hollow {
  //   border-color: $color-neutral-15;

  //   &:hover,
  //   &:focus {
  //     color: $color-neutral-60;
  //     background-color: $color-neutral-5;
  //     border-color: $color-neutral-15;
  //   }

  //   &:active {
  //     color: $color-neutral-60;
  //     background-color: $color-neutral-10;
  //   }
  // }

  &.pe-ant-btn-text,
  &.pe-btn-text {
    font-size: 14px;
    font-weight: 600;
    color: $color-neutral-60;
    background-color: $color-transparent;

    &:hover,
    &:focus,
    &:active {
      color: $color-neutral-60;
      background-color: $color-transparent;
    }

    &[disabled],
    &[disabled]:hover {
      color: $color-neutral-30;
    }
  }

  &.pe-ant-btn-icon-only.pe-ant-btn-sm {
    .anticon {
      vertical-align: -0.2em;
    }
  }

  &.pe-btn-danger {
    border-color: $color-neutral-15;
    color: $color-red;
  }
}

.pe-ant-btn > .anticon + span,
.pe-ant-btn > span + .anticon {
  margin-left: 5px;
}

// Typography
// -------------------------------------
p.pe-ant-typography {
  line-height: 150%;

  &.large {
    font-size: 16px;
  }
}

a.pe-ant-typography:hover {
  color: $color-primary-darker;
}

div {
  h1.pe-ant-typography,
  h2.pe-ant-typography,
  h3.pe-ant-typography,
  h4.pe-ant-typography,
  h5.pe-ant-typography,
  h6.pe-ant-typography {
    font-weight: 700;
    line-height: 150%;
  }

  .pe-ant-typography {
    line-height: 150%;

    &.small {
      font-size: 13px;
    }

    &.large {
      font-size: 16px;
    }

    &.pe-ant-typography-secondary {
      color: $color-neutral-50;

      &.dark {
        color: $color-neutral-60;
      }

      &.darker {
        color: $color-neutral-65;
      }

      &.light {
        color: $color-neutral-40;
      }
    }
  }
}

// checkbox and radio
// -------------------------------------
label {
  &.pe-ant-checkbox-wrapper {
    font-size: 14px;
    font-weight: 500 !important;
    align-items: center;

    > span {
      font-size: 14px;
      color: $color-neutral-50;
    }
  }

  .pe-ant-checkbox {
    top: 0;

    + span {
      padding-left: 12px;
    }

    .pe-ant-checkbox-inner {
      border-color: $color-neutral-20 !important;
      background-color: $color-white;

      &::after {
        left: 24%;
        width: 5px;
        height: 9px;
      }
    }

    &.pe-ant-checkbox-checked {
      .pe-ant-checkbox-inner {
        border-color: $color-primary !important;
      }
    }

    &.pe-ant-checkbox-indeterminate {
      .pe-ant-checkbox-inner {
        &::after {
          left: 50%;
          width: 8px;
          height: 2px;
        }
      }

      &.pe-ant-checkbox-disabled {
        .pe-ant-checkbox-inner {
          &::after {
            background-color: $color-neutral-40;
            border-color: $color-neutral-40;
          }
        }
      }
    }

    &.pe-ant-checkbox-disabled {
      .pe-ant-checkbox-input {
        margin-top: 1px !important;
        border-radius: 4px !important;
      }

      .pe-ant-checkbox-inner {
        background-color: $color-neutral-10;
      }

      + span {
        color: $color-neutral;
      }

      &.pe-ant-checkbox-checked {
        .pe-ant-checkbox-inner {
          &::after {
            border-color: $color-neutral-40;
          }
        }
      }
    }
  }

  .pe-ant-checkbox-checked::after {
    border: none !important;
  }

  // radio
  &.pe-ant-radio-wrapper {
    font-size: 14px;
    font-weight: 500 !important;
    color: $color-neutral-50;
    align-items: center;
  }

  .pe-ant-radio {
    top: 0;

    + span {
      padding-left: 12px;
    }

    &:hover {
      .pe-ant-radio-inner {
        border-color: $color-primary !important;
        transition: all 0.4s ease-in-out;
      }
    }

    .pe-ant-radio-inner {
      border-color: $color-neutral-20;
    }

    &.pe-ant-radio-checked {
      .pe-ant-radio-inner {
        border-color: $color-primary !important;
        border-width: 2px !important;
        box-shadow: none !important;
        outline: none !important;

        &::after {
          background-color: $color-primary !important;
        }
      }
    }

    &.pe-ant-radio-disabled {
      .pe-ant-radio-inner {
        background-color: $color-neutral-10;
        border-color: $color-neutral-20 !important;
      }

      + span {
        color: $color-neutral;
      }
    }
  }
}

.pe-ant-radio-group {
  &.pe-ant-radio-group-small {
    .pe-ant-radio-button-wrapper {
      padding-right: 20px;
      padding-left: 20px;
      font-size: 13px;
      font-weight: 600;

      &.pe-ant-radio-button-wrapper-checked {
        background: $color-primary;
        border: 1px solid $color-primary;
      }

      &:not(.pe-ant-radio-button-wrapper-checked) {
        background: $color-neutral-10;
        border: 1px solid $color-neutral-10;

        &:hover,
        &:focus {
          color: $color-neutral-60;
          background-color: $color-neutral-15;
        }

        &:active {
          color: $color-neutral-60;
          background-color: $color-neutral-10;
        }
      }
    }
  }

  &.pe-radio-group-secondary {
    .pe-ant-radio-button-wrapper {
      &:first-child {
        margin-right: 1px;
      }

      &:not(:first-child)::before {
        background: none;
      }

      &:not(.pe-ant-radio-button-wrapper-checked) {
        background: $color-neutral-10;
        border: 1px solid $color-neutral-10;

        &:hover,
        &:focus {
          color: $color-neutral-60;
          background-color: $color-neutral-15;
        }

        &:active {
          color: $color-neutral-60;
          background-color: $color-neutral-10;
        }
      }
    }
  }
}

// Input
// -------------------------------------
input,
textarea {
  &.pe-ant-input {
    min-height: auto;
    border-radius: 4px;
    line-height: 1.5;
    color: $color-neutral-50;
    border-color: $color-neutral-15;
    padding: 6px 10px;

    &-sm {
      padding: 3.5px 10px;
    }

    &-lg {
      font-size: 14px;
      padding: 8.5px 12px;
    }

    &:not([disabled]) {
      &:focus {
        border-color: $color-primary;
        box-shadow: 0 0 2px color.adjust($color-primary, $lightness: 20%);
      }
    }
  }

  &.pe-ant-input-number-input {
    color: $color-neutral-50;
  }
}

.pe-ant-input-number {
  border-color: $color-neutral-15;
}

// Form
// -------------------------------------
div.pe-ant-form-item {
  margin-bottom: 20px;

  // force the horizontal layout for an item in vertical form
  &.layout-horizontal {
    flex-direction: row;
    column-gap: 32px;
    align-items: center;

    .pe-ant-form-item-label {
      padding: 0;
      @include screen(mobile) {
        padding-bottom: 4px;
      }
    }
  }

  &-label {
    & > label {
      font-weight: 700;
      line-height: 140%;
    }

    span.anticon.pe-ant-form-item-tooltip {
      color: #e8e8eb;
    }
  }

  &-has-success {
    .pe-ant-form-item-children-icon {
      display: none;
    }

    &.show-success-feedback {
      .pe-ant-form-item-children-icon {
        display: block;
      }
    }
  }
}

.pe-ant-form-vertical {
  div.pe-ant-form-item {
    &-label {
      > label {
        height: auto;
      }
    }
  }
}

.pe-ant-form-large .pe-ant-form-item-label > label {
  height: 20px;
}

.pe-ant-form-item-explain {
  &.pe-ant-form-item-explain-error {
    color: $color-red !important;
  }
}

div.pe-ant-form-item-extra {
  min-height: 16px;
}

.pe-input-counter {
  .pe-ant-input-suffix {
    color: $color-neutral-20;
  }
}

.pe-input-label {
  position: relative;
  display: inline-flex;
  align-items: center;
  max-width: 100%;
  height: 40px;
  font-size: 15px;
  font-weight: 600;
  color: $color-neutral;
}

.pe-ant-typography.label-large,
.label-large {
  font-size: 15px;
  line-height: 131%;
  font-weight: 600;
  color: $color-neutral;
  display: flex;
  align-items: center;
}

// ----Select-----
// -------------------------------------
div.pe-ant-select {
  color: $color-neutral-50;

  &-dropdown {
    padding: 6px 0;
    border: solid 1px $color-neutral-15;
    box-shadow: 0 3px 2px rgb(0 0 0 / 5%);
    border-radius: 3px;
    z-index: 100050;
  }

  &-lg {
    font-size: 14px;
  }

  &-item {
    min-height: 32px;
    color: $color-neutral-60;

    &-option-disabled {
      color: $color-neutral-15;
    }

    &-option-selected:not(.pe-ant-select-item-option-disabled) {
      font-weight: 400;
      background-color: #fafaff;
      color: $color-primary;
    }
  }

  &:not(.pe-ant-select-customize-input) {
    .pe-ant-select-selector {
      .pe-ant-select-selection-search-input {
        box-shadow: none;
        min-height: 27px;
      }
    }
  }

  &-sm {
    &:not(.pe-ant-select-customize-input) {
      .pe-ant-select-selector {
        .pe-ant-select-selection-search-input {
          min-height: 18px;
        }
      }
    }
  }

  &-multiple {
    .pe-ant-select-selection-item {
      background-color: $color-neutral-3;
      border-radius: 3px;
      color: #656470;
      font-weight: 700;
      font-size: 13px;
    }
  }

  span.pe-ant-select-arrow {
    span.anticon-down {
      @include replace-form-feedback-icon(
        "data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 11 7' fill='none'%3E%3Cpath d='M5.21937 6.01002C5.32752 6.08999 5.47892 6.08999 5.58707 6.01002L10.86 1.11233L9.69661 0.0599802C9.58846 -0.0199934 9.41544 -0.0199934 9.32892 0.0599802L5.3924 3.67879L1.47752 0.0599802C1.391 -0.0199934 1.21797 -0.0199934 1.10983 0.0599802L0 1.11233L5.21937 6.01002Z' fill='%23656470'/%3E%3C/svg%3E"
      );
    }
  }

  span.pe-ant-select-selection-item-remove {
    span.anticon-close {
      @include replace-form-feedback-icon(
        "data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8' fill='none'%3E%3Cpath d='M7 1.60429L6.39571 1L4 3.39571L1.60429 1L1 1.60429L3.39571 4L1 6.39571L1.60429 7L4 4.60429L6.39571 7L7 6.39571L4.60429 4L7 1.60429Z' fill='%23656470' stroke='%23656470'/%3E%3C/svg%3E"
      );
    }
  }

  span.pe-ant-select-selection-item-content {
    margin-right: 5px;
  }
}

.pe-ant-single-select-box {
  .pe-ant-select-item-option-selected:not(.pe-ant-select-item-option-disabled)
    .pe-ant-select-item-option-state::after {
    content: '\2713';
    display: inline-block;
    color: $color-primary;
  }
}

// -----------Menu----------
.pe-ant-menu-item {
  border-radius: 4px;
}

// ------Modal------
div.pe-ant-modal {
  &-mask {
    background-color: rgb(20 27 57 / 30%) !important;
    z-index: 100000;
  }

  &-wrap {
    z-index: 100000;
  }

  // remove header and footer border line
  &.no-border {
    .pe-ant-modal {
      &-header {
        border-bottom: none;
      }

      &-footer {
        border-top: none;
      }
    }
  }

  .pe-ant-modal-close {
    color: #8c8f9a;

    &:hover {
      color: $color-neutral-50;
    }

    .pe-ant-modal-close-x {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

// -------Notification-----
div.pe-ant-notification {
  z-index: 100010;
}

// ---------Alert---------
// customize the close icon
.pe-alert {
  .pe-ant-alert-close-icon {
    position: absolute;
    top: -10px;
    right: -11px;
    padding: 6px;
    font-size: 8px;
    line-height: 8px;
    background: #fff;
    border: solid 1px #fff;
    border-radius: 50%;
  }
}

/** Card */
// -------------------------------------
.pe-ant-card {
  box-shadow: 0 3px 2px rgb(0 0 0 / 5%);

  .pe-ant-card-head {
    font-weight: 700;
    border-bottom: 1px solid $color-neutral-5;
  }
}

.pe-card-xs {
  border-radius: 3px;
  box-shadow: 0 3px 2px rgb(0 0 0 / 5%);

  .pe-ant-card-body {
    padding: 20px 30px;
  }
}

/** Table */
// -------------------------------------
.pe-ant-table-empty {
  .pe-ant-table-tbody > tr.pe-ant-table-placeholder {
    text-align: left !important;
    font-size: 13px;
    font-weight: 400px;
    color: $color-neutral-40 !important;
  }
}

/** Drawer */
// -------------------------------------
.pe-ant-drawer {
  z-index: 100000;
}

/** Badge */
// -------------------------------------
.pe-badge {
  padding: 3px 8px;
  font-size: 12px;
  font-weight: 600;
  line-height: 100%;
  color: $color-primary;
  text-align: center;
  background: color.adjust($color-primary, $lightness: 37%);
  border-radius: 50px;

  &-primary {
    color: $color-primary;
    background: color.adjust($color-primary, $lightness: 37%);
  }

  &-secondary {
    color: $color-neutral-40;
    background: $color-neutral-10;
  }

  &-pause {
    color: $color-primary;
    background: '#3B43FF';
  }

  &-green {
    color: $color-green;
    background: color.adjust($color-green, $lightness: 62%);
  }

  &-red {
    color: $color-red;
    background: rgb(223 42 74 / 5%);
  }
}

/** Tooltip */
// -------------------------------------
.pe-ant-tooltip {
  z-index: 100070;
  font-size: 13px;

  .pe-ant-tooltip-arrow-content::before {
    clip-path: inset(33% 33%);
  }
}

/** Divider */
// -------------------------------------
div.pe-ant-divider {
  .pe-ant-divider-inner-text {
    color: #454545;
    font-size: 12px;
    border-radius: 10px;
    padding-left: 5px;
    padding-right: 5px;
    background: #f5f5f5;
    font-weight: 500;
  }
}

// Steps
// -------------------------------------
.pe-ant-steps-container {
  .pe-ant-step-divider {
    max-width: 99px !important;
  }
}

// Ant Tooltip PeCircleQuestionFilled Icon
// ----------------------------------------------
.anticon {
  [data-icon="pe-circle-question-filled"] {
    color: $color-neutral-40;
  }
}
