@import '../../styles/variables';
@import '../../styles/mixins';

.pe-campaign-dropdown-menu {
  border: solid 1px $color-neutral-15;
  box-shadow: 0 3px 2px rgb(0 0 0 / 5%) !important;
  width: 212px;

  &.pe-ant-dropdown-menu {
    padding: 12px 0;

    .pe-ant-dropdown-menu {
      &-item {
        padding: 6px 12px 5px 24px;
        color: $color-neutral-50;

        &-active,
        &:hover {
          color: $color-primary;
          background-color: #fafaff;

          .pe-ant-dropdown-menu-title-content {
            .anticon,
            span {
              color: $color-primary;
            }
          }
        }
      }

      &-title-content {
        font-weight: 500;

        .anticon {
          margin-right: 14px;
          color: $color-neutral-50;
        }
      }
    }
  }

  &-btn:not[disabled] {
    &.pe-ant-dropdown-trigger {
      &:hover,
      &:active,
      &.pe-ant-dropdown-open {
        background-color: $color-primary-darker;
      }
    }
  }
}

.pe-dashboard-recent-notifications {
  max-height: 553px;
  @include screen(tablet) {
    max-height: none;
  }

  .pe-ant-collapse-content {
    max-height: 492px;
    @include screen(tablet) {
      max-height: none;
    }

    .pe-ant-collapse-content-box {
      padding-top: 4px;
    }

    .pe-ant-list-item {
      padding-top: 15px;
      padding-bottom: 20px;
      margin-bottom: 0;

      .pe-ant-list-item-meta-title {
        margin-bottom: 9px;
      }
    }

    .pe-ant-list-footer {
      padding-bottom: 0;
      padding-top: 0;
    }
  }
}

.pe-dashboard-empty-notifications {
  max-height: 390px;

  .pe-ant-collapse-content {
    height: 322px;
  }
}

.pe-dashboard-strategies {
  max-height: 553px;
  @include screen(tablet) {
    max-height: none;
  }

  .pe-ant-collapse-content {
    min-height: 492px;

    .pe-ant-collapse-item {
      border-bottom: unset;
    }
  }
}

.pe-dashboard-support {
  max-height: 390px;
  @include screen(tablet) {
    max-height: none;
  }

  .pe-ant-collapse-content {
    height: 322px;
  }
}

.pe-recent-notification-dropdown-menu {
  border: solid 1px $color-neutral-15;
  background: $color-white;
  box-shadow: 0 3px 2px rgb(0 0 0 / 5%) !important;
  width: 149px;
  border-radius: 3px !important;

  &.pe-ant-dropdown-menu {
    padding: 0;

    .pe-ant-dropdown-menu {
      &-item {
        height: 32px;
        padding: 11px 10px;
        color: $color-neutral-40;
        font-size: 13px;
        font-weight: 500;
        line-height: 150%;

        &-active,
        &:hover,
        &:focus,
        &:active {
          color: $color-primary;
          background-color: #fafaff;
        }
      }
    }
  }

  &-btn {
    .pe-ant-dropdown-trigger {
      width: 32px;
      border-left-color: $color-primary !important;

      &:hover,
      &:active,
      &.pe-ant-dropdown-open {
        background-color: $color-primary-darker;
      }
    }
  }
}
