@import './variables';

.pe-modal-form {
  .pe-ant-modal-header {
    padding: 20px 24px;
    border-bottom-color: $color-neutral-5;
  }

  .pe-ant-modal-title {
    font-size: 18px;
    line-height: 130%;
    font-weight: 700;
  }

  .pe-ant-modal-body {
    padding-top: 0;
    padding-bottom: 0;
    overflow-y: auto;
    max-height: 90vh;
  }
}
