.notification__preview.ios--web {
  font-family: -apple-system, BlinkMacSystemFont, system-ui, 'Myriad Pro', myriad-pro, 'Segoe UI',
    Roboto, Ubuntu, 'Helvetica Neue', sans-serif;
  max-width: 360px;
  box-shadow: 0 0 32px 0 rgb(0 0 0 / 6%);
  padding: 10px 12px;
  align-items: center;
  border-radius: 20px;
  line-height: 100%;
  backdrop-filter: blur(16px);
  background-color: rgb(255 255 255 / 50%);
  cursor: default;
  user-select: none;

  .notification-app-logo {
    width: 34px;
    height: 34px;
    display: flex;
    align-items: center;
    justify-content: center;

    .notification-app-logo-img {
      width: 32px;
      height: 32px;
      border-radius: 8px;
      object-fit: cover;
    }

    .notification-app-logo-img-placeholder {
      width: 32px;
      height: 32px;
      background-color: #041c61;
      color: #fff;
      border-radius: 8px;
      font-size: 18px;
      font-weight: 600;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .notification-content {
    margin-left: 10px;

    .notification-title,
    .notification-app-name,
    .notification-message {
      line-height: 16px;
      font-weight: 500;
      letter-spacing: -0.09px;
      color: #000;
    }

    .notification-message {
      font-weight: 300;
      max-height: 75px;
      font-size: 13.5px;
    }
  }
}
