.opt-in__floating-bar {
  @include reset-opt-in-style;

  backdrop-filter: blur(2px);
  background-color: var(--opt-in-background-color);
  margin: 0;
  right: 0;
  visibility: visible !important;
  max-width: var(--opt-in-width);
  width: 100%;
  left: 0;
  position: absolute;
  max-height: 100%;
  overflow-y: hidden;
  border-radius: 0;

  &.opt-in__floating-bar--light {
    color: $default-dark-color;
  }

  &.opt-in__floating-bar--dark {
    color: $default-light-color;
  }

  &.opt-in__floating-bar--pe-bottom {
    bottom: 0;
    box-shadow: 0px -2px 4px rgba(121, 130, 154, 0.1);

    &.opt-in__floating-bar--mobile_test_view {
      left: 13px;
      right: 13px;
      width: calc(100% - 26px);
      border-radius: 0 0 22px 22px;
      bottom: 15px;
    }
  }

  &.opt-in__floating-bar--pe-top {
    top: 0;
    box-shadow: 0px 2px 2px rgba(121, 130, 154, 0.1);

    &.opt-in__floating-bar--desktop_test_view {
      border-top: 1px solid #ece9e9;
      top: 50px;
    }

    &.opt-in__floating-bar--mobile_test_view {
      top: 41px;
      left: 13px;
      right: 13px;
      width: calc(100% - 26px);
    }
  }

  p {
    @include reset-opt-in-paragraph-style;
  }

  &.opt-in__floating-bar--desktop {
    align-items: flex-start;
    display: flex;
    flex-direction: row;
    padding: 18px 20px;

    .opt-in__floating-bar__powered-by {
      @include opt-in-powered-by;

      bottom: 4.5px;
      right: 20px;
    }

    .opt-in__floating-bar__content {
      display: flex;
      flex-flow: row nowrap;
      width: 100%;

      .opt-in__floating-bar__icon__container {
        @include opt-in-icon-container;

        display: flex;
        flex-shrink: 0;

        &.opt-in__floating-bar__icon__container--default-icon {
          @include opt-in-icon-container-default-icon;
        }

        .opt-in__floating-bar__icon {
          @include opt-in-icon;
        }
      }

      .opt-in__floating-bar__title {
        @include opt-in-title;

        align-items: center;
        display: flex;
        line-height: 20px;
        margin: 0 12px;

        p {
          line-height: 20px;
        }
      }

      .opt-in__floating-bar__segments {
        @include opt-in-segment-body-scroll;

        margin: 0 0 0 12px;
      }


    }

    .opt-in__floating-bar__button {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: flex-end;
      flex-shrink: 0;

      .opt-in__floating-bar__button__allow,
      .opt-in__floating-bar__button__close {
        @include opt-in-action-button;
      }

      .opt-in__floating-bar__button__allow {
        background: var(--allow-btn-background-color);
        color: #fff;
        margin: 0 10px 0 0;
      }

      .opt-in__floating-bar__button__close {
        background-color: var(--close-btn-background-color);
        border: var(--close-btn-border);
        color: #677489;
      }
    }
  }

  &.opt-in__floating-bar--mobile {
    $opt-in-padding: 16px;

    padding: $opt-in-padding;
    display: flex;
    flex-flow: column nowrap;

    .opt-in__floating-bar__powered-by {
      @include opt-in-powered-by;

      bottom: 3.5px;
    }

    .opt-in__floating-bar__content {
      display: flex;
      flex-flow: row nowrap;
      width: 100%;

      .opt-in__floating-bar__icon__container {
        @include opt-in-icon-container;

        display: flex;
        flex-shrink: 0;
        margin-right: 12px;

        &.opt-in__floating-bar__icon__container--default-icon {
          @include opt-in-icon-container-default-icon;
        }

        .opt-in__floating-bar__icon {
          @include opt-in-icon;
        }
      }

      .opt-in__floating-bar__title {
        @include opt-in-title;

        display: block;
        line-height: 20px;

        p {
          line-height: 20px;
        }
      }
    }

    .opt-in__floating-bar__segments {
      @include opt-in-segment-body-scroll;

      display: flex;
      flex-wrap: wrap;
      margin: 17px 0 1px 60px;
      margin-right: -$opt-in-padding;
      padding-right: $opt-in-padding;

      .opt-in__checkbox {
        flex-basis: calc(50% - 8px);

        &:nth-child(even) {
          margin-left: 16px;
        }
      }
    }

    .opt-in__floating-bar__button {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-end;
      margin-top: 11px;

      .opt-in__floating-bar__button__allow,
      .opt-in__floating-bar__button__close {
        @include opt-in-action-button;
      }

      .opt-in__floating-bar__button__allow {
        background: var(--allow-btn-background-color);
        color: #fff;
        margin-right: 8px;
      }

      .opt-in__floating-bar__button__close {
        background-color: var(--close-btn-background-color);
        border: var(--close-btn-border);
        color: #677489;
      }
    }
  }
}
