@import './mixins';
@import './variables';

.pe-confirmation-modal {
  .pe-ant-modal-body {
    padding: 36px 46px;
    @include screen(mobile) {
      padding: 32px 24px;
    }
  }

  .pe-ant-modal-confirm-body .pe-ant-modal-confirm-title {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 18px;
    text-align: center;
  }

  .pe-ant-modal-confirm-content {
    font-size: 14px;
    font-weight: 400;
    color: $color-neutral-40;
    line-height: 21px;
    text-align: center;
  }

  .pe-ant-modal-confirm-btns {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row-reverse;

    .pe-ant-btn {
      margin-right: 15px;
      padding: 8px 30px;
    }

    .pe-ant-btn-dangerous {
      background-color: $color-red;
      border: 1px solid $color-red;
      color: $color-white !important;
    }
  }

  .pe-ant-modal-wrap {
    text-align: center;
  }
}
