.opt-in__safari-style {
  $opt-in-padding: 16px;

  @include reset-opt-in-style;

  background-color: var(--opt-in-background-color);
  margin: 0;
  padding: $opt-in-padding;
  position: absolute;
  visibility: visible !important;
  width: var(--opt-in-width);
  display: flex;
  flex-flow: column nowrap;
  max-height: 100%;
  overflow-y: hidden;

  &.opt-in__safari-style--light {
    color: $default-dark-color;
  }

  &.opt-in__safari-style--dark {
    color: $default-light-color;
  }

  &.opt-in__safari-style--pe-top-left {
    border-radius: 0 0 12px 12px;
    box-shadow: 2px 2px 5px $box-shadow-color;
    left: 0;
    top: 0;

    &.opt-in__safari-style--desktop_test_view {
      top: 50px;
    }
  }

  &.opt-in__safari-style--pe-top-right {
    border-radius: 0 0 12px 12px;
    box-shadow: -2px 2px 5px $box-shadow-color;
    right: 0;
    top: 0;

    &.opt-in__safari-style--desktop_test_view {
      top: 50px;
    }
  }

  &.opt-in__safari-style--pe-top-center {
    border-radius: 0 0 12px 12px;
    box-shadow: 2px 2px 5px $box-shadow-color;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0);

    &.opt-in__safari-style--desktop_test_view {
      top: 50px;
    }
  }

  &.opt-in__safari-style--pe-mobile-bottom {
    border-radius: 12px 12px 0 0;
    bottom: 0;
    box-shadow: 2px -2px 5px $box-shadow-color;
    left: 50%;
    transform: translate(-50%, 0);

    &.opt-in__safari-style--mobile_test_view {
      bottom: 14px;
    }
  }

  &.opt-in__safari-style--pe-mobile-top {
    border-radius: 0 0 12px 12px;
    box-shadow: 2px 2px 5px $box-shadow-color;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);

    &.opt-in__safari-style--mobile_test_view {
      top: 41px;
    }
  }

  .opt-in__safari-style__content {
    display: flex;
    flex-flow: row nowrap;
    width: 100%;

    .opt-in__safari-style__icon__container {
      @include opt-in-icon-container;

      display: flex;
      flex-shrink: 0;
      margin-right: 13px;

      &.opt-in__safari-style__icon__container--default-icon {
        @include opt-in-icon-container-default-icon;
      }

      .opt-in__safari-style__icon {
        @include opt-in-icon;
      }
    }

    .opt-in__safari-style__title {
      @include opt-in-title;

      display: block;
      line-height: 18px;

      p {
        line-height: 18px;
      }
    }
  }

  .opt-in__safari-style__segments {
    @include opt-in-segment-body-scroll;

    display: flex;
    flex-wrap: wrap;
    margin-top: 17px;
    margin-bottom: 1px;
    margin-right: -$opt-in-padding;
    padding-right: $opt-in-padding;

    .opt-in__checkbox {
      flex-basis: calc(50% - 8px);

      &:nth-child(even) {
        margin-left: 16px;
      }
    }
  }

  .opt-in__safari-style__button {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    margin-top: 11px;

    .opt-in__safari-style__button__allow,
    .opt-in__safari-style__button__close {
      @include opt-in-action-button;
    }

    .opt-in__safari-style__button__allow {
      background: var(--allow-btn-background-color);
      color: #fff;
      margin-right: 8px;
    }

    .opt-in__safari-style__button__close {
      background-color: var(--close-btn-background-color);
      border: var(--close-btn-border);
      color: #677489;
    }
  }

  .opt-in__safari-style__powered-by {
    @include opt-in-powered-by;

    bottom: 3.5px;
  }

  p {
    @include reset-opt-in-paragraph-style;
  }
}
