/* stylelint-disable selector-class-pattern */
@import '../../../styles/variables';

.stepNotification {
  list-style: none;
  width: 80%;
  flex: 1;
  padding: 8px;
  transition: box-shadow 0.2s ease-in-out;
  display: flex;
  flex-direction: column;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 0;
    margin: 0;
    list-style: none;
    padding-top: 30px;
  }

  &__dragging {
    list-style: none;
    z-index: 99999;
    border: 1px dashed #e8e8e8;
    transition: box-shadow 0.2s ease-in-out;
    padding: 10px;

    .pe-notification-label {
      margin-bottom: 10px;
    }

    .pe-notification-builder-index {
      display: none;
    }

    .pe-move-next-prev {
      display: none;
    }

    .pe-notification-report {
      padding: 18px 0 18px 10px;
      display: flex;
      flex-direction: column;

      .pe-stats-column {
        display: flex;
        justify-content: space-evenly;

        p {
          margin: 0;
          padding: 0;
        }
      }
    }
  }

  &__wrap {
    position: relative;
    width: 100%;
    padding-left: 40px;
    overflow: hidden;
    padding-bottom: 32px;

    &:last-child {
      .stepNotification__index::before {
        height: 0;
        top: 60px;
        transition: all 0.8s ease;
      }
    }

    &.stepNotification__no-drag {
      &:last-child {
        .stepNotification__index::before {
          height: 0;
          top: 36px;
          transition: all 0.8s ease;
        }
      }
    }
  }

  &__no-drag {
    .stepNotification__index::before {
      height: calc(100% - 40px);
      top: 36px;
    }
  }
}

.pe-analytics-card {
  .ant-card-head-title {
    font-size: 16px;
    font-weight: 600;
    padding-top: 10px;
    padding-bottom: 10px;
    color: $color-neutral-60;
  }

  .ant-statistic {
    display: flex;
    flex-flow: column-reverse;
  }
}

.pe-automation-details-drawer {
  max-width: 100%;

  .pe-ant-drawer-header {
    background-color: $color-neutral-3;
  }

  .pe-ant-drawer-content-wrapper {
    max-width: 100%;
  }

  .pe-ant-drawer-header-title {
    flex-direction: row-reverse;
  }
}
