.opt-in__chrome-mobile {
  box-shadow: 0 2px 2px rgba(121, 130, 154, 0.1);

  &.pe-ant-card {
    width: 90%;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
    overflow: hidden;

    .pe-ant-card-body {
      padding: 24px;
    }
  }

  .pe-ant-btn {
    box-shadow: none;
    border: 0;
    color: #1c70e9;
    font-size: 14px;
  }
}

.opt-in__chrome-mobile__tooltip {
  .opt-in__chrome-mobile__tooltip__title {
    @include tooltip-title;
  }

  .opt-in__chrome-mobile__tooltip____powered-by {
    @include tooltip-powered-by;
  }
}
