@import '../../styles/variables';

.pe-plugin-recommendation-card {
  .pe-ant-card-body {
    height: 200px;

    .pe-ant-card-meta {
      .pe-ant-card-meta-avatar {
        padding-right: 12px;
      }

      .pe-ant-card-meta-title {
        font-weight: 700;
        font-size: 16px;
        line-height: 150%;
        margin-bottom: 5px;
      }

      .pe-ant-card-meta-description {
        font-weight: 500;
        font-size: 14px;
        line-height: 170%;
        color: $color-neutral-50;
      }
    }
  }

  .pe-ant-card-actions {
    height: 48px;
    margin-bottom: 0;
    background: $color-neutral-3;

    li {
      margin: 0;

      &:not(:last-child) {
        border-right: unset;
      }

      &:first-child {
        text-align: left;
        padding: 14px 18px;
      }

      &:last-child {
        text-align: right;
        padding: 9px 18px;
      }

      > span {
        cursor: default;
      }
    }
  }
}

.pe-plugin-documentation-card {
  min-height: 274px;
  box-shadow: none !important;
  border-color: $color-neutral-5;

  @media screen and (max-width: 1050px) {
    height: 100%;
  }

  .pe-ant-card-body {
    padding: 35px 52px;
  }

  .pe-plugin-documentation-card-container {
    display: flex;
    gap: 60px;

    @media screen and (max-width: 1050px) {
      gap: 0;
      flex-direction: column;
      align-items: center;

      .pe-plugin-documentation-card-container-text {
        .pe-ant-typography {
          text-align: center;
        }

        .pe-ant-btn {
          display: block;
          margin: auto;
        }
      }
    }
  }
}
