.pe-statics-cards {
  justify-content: space-between;

  @media only screen and (max-width: 1400px) {
    justify-content: start;
  }

  .pe-statics-card {
    flex: 1 1 auto;
  }
}

.pe-collapsible-card {
  border: none;
  box-shadow: 0 3px 2px rgb(0 0 0 / 5%);
}

.pe-site-connect-alert-card {
  .pe-ant-card-body {
    min-height: 153px;
    padding: 24px 40px 24px 22px;

    .pe-ant-card-meta-avatar {
      padding-right: 0;
      margin-right: 26px;
    }

    .pe-ant-card-meta-detail {
      .pe-ant-card-meta-title {
        font-size: 19px;
        font-weight: 700;
        line-height: 150%;
        margin-bottom: 6px;
        white-space: normal;
      }
    }
  }
}

// Stepdown
.analytics-stepdown {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 9px;
  clip-path: polygon(0% 0%, 100% 0%, 95% 100%, 5% 100%);
  font-size: 15px;
  font-weight: 600;

  &-flexbox {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &-1 {
    width: 320px;
    height: 45px;
    color: #fff;
    background: #696dd0;
  }

  &-2 {
    width: 286px;
    height: 45px;
    color: #fff;
    background: #454bcd;
  }

  &-3 {
    width: 256px;
    height: 45px;
    color: #fff;
    background: #2b31b3;
  }

  &-tag {
    font-size: 14px;
    font-weight: 700;
    padding: 5px 5px 3px;
    line-height: 100%;
    margin-right: 10px;
    color: $color-primary-60;
    background: #fff;
    border: 1px solid #fff;
    border-radius: 3px;
  }
}
