@import '../../styles//variables';
@import '../../styles//mixins';

.pe-recommended-features-container {
  .pe-ant-list-item-meta-title {
    font-size: 16px;
    font-weight: 700;
  }

  .pe-ant-list-item-meta-description {
    padding-right: 65px;
    color: $color-neutral-60;
    line-height: 140%;
    @include screen(mobile) {
      padding-right: 20px;
    }
  }

  .pe-ant-list-item {
    padding: 12px 0 16px;
    border-bottom: 1px solid #f1f1f1;
  }

  .pe-ant-checkbox-circle {
    .pe-ant-checkbox-inner {
      height: 24px;
      width: 24px;
      border-radius: 50%;
    }

    .pe-ant-checkbox-checked {
      .pe-ant-checkbox-inner {
        background-color: #087ce1 !important;
        border-color: #087ce1 !important;
        outline: none;
        box-shadow: none;

        &::after {
          position: absolute;
          display: table;
          border: 2px solid $color-white;
          border-top: 0;
          border-left: 0;
          transform: rotate(45deg) scale(1) translate(-50%, -50%);
          opacity: 1;
          transition: all 0.2s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s;
          content: ' ';
        }
      }
    }

    .pe-ant-checkbox-inner::after {
      left: 30%;
    }
  }

  .pe-ant-checkbox-primary {
    .pe-ant-checkbox-checked {
      .pe-ant-checkbox-inner {
        background-color: #46b450 !important;
        border-color: #46b450 !important;
        outline: none;
        box-shadow: none;
      }
    }
  }
}
