.pe-date-range-dropdown-btn-menu {
  &.pe-ant-dropdown-menu {
    min-width: 167px;
    padding: 6px 0 10px;
    border: solid 1px $color-neutral-5;
    border-radius: 3px;
    box-shadow: 0 3px 2px rgb(0 0 0 / 5%);

    .pe-ant-dropdown-menu-item {
      padding: 8px 10px 7px;

      &-active {
        color: $color-primary;
        background-color: #f5f6ff;
      }

      .pe-ant-dropdown-menu-title-content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 13px;
        line-height: 130%;
      }
    }
  }
}

div.pe-date-range-dropdown-btn {
  &-overlay {
    .pe-ant-popover-arrow {
      display: none;
    }

    .pe-ant-popover-inner-content {
      padding: 16px;
    }

    &-content {
      position: relative;
      display: flex;
      align-items: flex-end;
      min-height: 340px;
    }

    &-footer {
      padding: 8px 0;
      border-top: solid 1px $color-neutral-3;

      .pe-ant-picker-range {
        width: 168px;
        padding-right: 8px;
        padding-left: 8px;
        padding-bottom: 0;

        .pe-ant-picker-range-separator {
          padding: 0 8px;
        }

        .pe-ant-picker-input > input {
          text-align: center;
          font-size: 13px;
          font-weight: 500;
        }
      }
    }
  }

  &-picker-dropdown {
    padding: 0;

    .pe-ant-picker-range-arrow {
      display: none;
    }

    .pe-ant-picker-panel-container {
      border: none;
      box-shadow: none;

      .pe-ant-picker-panels {
        .pe-ant-picker-panel {
          border: none;

          // padding-right: 16px;
          // padding-left: 0;

          .pe-ant-picker-date-panel {
            // width: 317px;
          }

          .pe-ant-picker-header {
            border: none;

            .pe-ant-picker-header-next-btn {
              visibility: visible !important;
            }

            .pe-ant-picker-header-super-next-btn,
            .pe-ant-picker-header-super-prev-btn {
              display: none;
            }
          }

          .pe-ant-picker-body {
            padding: 0;
          }
        }

        .pe-ant-picker-panel:not(:first-child) {
          display: none;
        }
      }
    }
  }
}

// Date picker common style
div.pe-ant-picker-dropdown {
  z-index: 100070;
}

.pe-ant-picker-panel-container {
  border: solid 1px $color-neutral-15;

  .pe-ant-picker-footer {
    line-height: 20px;

    ul.pe-ant-picker-ranges {
      margin-bottom: 0;
      line-height: inherit;
    }
  }
}

div.pe-ant-picker-time-panel {
  .pe-ant-picker-header {
    .pe-ant-picker-header-view {
      font-weight: 600;
    }
  }
}

div.pe-ant-picker-date-panel {
  .pe-ant-picker-header {
    .pe-ant-picker-header-view {
      font-size: 18px;
      font-weight: 700;
    }
  }

  .pe-ant-picker-body {
    padding: 16px;

    .pe-ant-picker-content {
      width: 100%;
      font-size: 13px;

      thead {
        background-color: $color-neutral-10;
        border-radius: 2px;

        tr {
          th {
            font-weight: 500;
            color: $color-neutral-40;
            background: $color-white;
          }
        }
      }

      tbody {
        td.pe-ant-picker-cell {
          padding: 1px 0;
          font-weight: 600;

          &::before {
            height: 32px;
          }

          .pe-ant-picker-cell-inner {
            width: 32px;
            height: 32px;
            line-height: 32px;
            border-radius: 50%;
            font-weight: 500;

            &::before {
              border-radius: 50%;
            }
          }

          &.pe-ant-picker-cell-today {
            .pe-ant-picker-cell-inner {
              border: 1px solid $color-accent-lighter;
              background: $color-white !important;
              color: $color-accent-lighter;

              &::before {
                border: unset;
              }
            }
          }

          &.pe-ant-picker-cell-range-start {
            .pe-ant-picker-cell-inner {
              border: 1px solid $color-accent-lighter;
              background: $color-white !important;
              color: $color-accent-lighter;

              &::before {
                border: unset;
              }
            }

            &:not(.pe-ant-picker-cell-range-start-single)::before {
              background: #fafaff;
            }
          }

          &.pe-ant-picker-cell-range-end {
            &:not(.pe-ant-picker-cell-range-end-single)::before {
              background: #fafaff;
            }
          }

          &.pe-ant-picker-cell-in-range {
            background: #fafaff;

            // change todays date background
            &.pe-ant-picker-cell-today {
              .pe-ant-picker-cell-inner {
                border: unset;
                background: #fafaff !important ;
                color: unset;

                &::before {
                  border: unset;
                }
              }
            }
          }

          &.pe-ant-picker-cell-in-view.pe-ant-picker-cell-in-range::before {
            background: #fafaff;
          }
        }

        td.pe-ant-picker-cell-today:not(.pe-ant-picker-cell-selected) {
          .pe-ant-picker-cell-inner {
            background-color: $color-neutral-10;
          }
        }
      }
    }
  }
}
