@import '../../../styles/variables';

$color-new: #af7533;

.pe-notification-status {
  color: $color-neutral-60 !important;
  font-size: 13px !important;

  &-sending,
  &-sent {
    color: $color-green-60 !important;
  }

  &-draft {
    color: $color-new !important;
  }

  &-scheduled,
  &-repeat_scheduled {
    color: $color-primary !important;
  }
}
