.pe-popup-modal-card {
  box-shadow: 0 3px 2px rgb(0 0 0 / 5%);
  border-radius: 4px;

  .pe-popup-modal-content {
    cursor: pointer;

    .pe-popup-modal-img-content {
      display: block;
      padding: 26px 42px;
      background-color: #f7f8fb;
    }

    .pe-popup-modal-hover-content {
      width: 258px;
      height: 172px;
      background: #646970;
      display: none;
    }

    &:hover {
      .pe-popup-modal-hover-content {
        display: block;
      }

      .pe-popup-modal-img-content {
        display: none;
      }
    }

    .pe-popup-modal-tooltip-active {
      display: block;

      &.pe-popup-modal-img-content {
        display: none;
      }
    }
  }
}

.pe-design-mobile-preview-modal {
  .pe-ant-modal-content {
    background-image: var(--mobile-preview-background-image);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-radius: 35px;

    .pe-ant-modal-close {
      top: -5px;

      .pe-ant-modal-close-x {
        background: black;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        line-height: 50px;

        .anticon {
          color: #fff;
          font-size: 17px;
          font-weight: normal;
        }
      }
    }
  }
}

.pe-chrome-desktop-optin-preview {
  &.pe-ant-card {
    width: 340px;
    position: absolute;
    left: 0;
    top: 0;
  }

  .pe-ant-card-body {
    padding: 6px 10px 15px;
  }

  .pe-close-icon-preview {
    position: absolute;
    right: 12px;
  }

  .pe-title-preview {
    margin-top: 14px;

    .pe-ant-typography {
      font-size: 16px;
      margin-bottom: 5px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  .pe-message-preview {
    .pe-ant-typography {
      font-size: 15px;
      opacity: 0.8;
    }
  }

  .pe-action-preview {
    .pe-ant-btn {
      margin: 0 5px;
      color: #1890ff;
      padding: 4.3px 15px;
    }
  }
}

.pe-widget-bell-preview {
  z-index: 1;
  bottom: 30px;
  position: absolute;
  cursor: pointer;
  margin: 0 20px;
}

.pe-safari-preview {
  &.pe-ant-card {
    width: 370px;
    padding: 15px 10px;
    position: absolute;
  }

  .pe-ant-card-body {
    padding: 0 !important;
  }

  .pe-image-preview {
    img {
      max-width: 60px;
      max-height: 60px;
    }
  }

  .pe-title-preview {
    word-wrap: break-word;
    max-height: 60px;
    height: 60px;
    min-height: 60px;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 20px;
    color: #232323;
    font-size: 15px;

    .pe-ant-typography {
      margin: 0;

      p {
        margin-bottom: 0;
      }
    }
  }

  .pe-ant-btn {
    margin-top: 10px;
    max-width: 120px;
    max-height: 30px;
    padding: 0 5px;
    min-width: 60px;
    text-align: center;
    border-radius: 2px;
    font-size: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: break-word;
    line-height: 30px;

    p {
      margin-bottom: 0;
      line-height: 30px;
      display: inline-block;
    }
  }

  .pe-allow-btn-preview {
    &.pe-ant-btn {
      background-color: #2ecc71;
      color: #fff;
      border: 0;
    }
  }

  .pe-close-btn-preview {
    &.pe-ant-btn {
      background-color: #fff;
      color: #000;
      margin-right: 10px;
      border: 0;
    }
  }
}

.pe-floating-bar-preview {
  &.pe-ant-card {
    width: 100%;
    padding: 15px 10px;
    position: absolute;
  }

  .pe-ant-card-body {
    padding: 0 !important;
  }

  .pe-image-preview {
    width: 80px;
    height: 60px;
    text-align: center;

    img {
      max-width: 60px;
      max-height: 60px;
    }
  }

  .pe-title-preview {
    word-wrap: break-word;
    max-height: 60px;
    height: 60px;
    min-height: 60px;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 20px;
    color: #232323;
    font-size: 19px;
    width: 58%;

    .pe-ant-typography {
      margin: 0;

      p {
        margin-bottom: 0;
      }
    }
  }

  .pe-ant-btn {
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: break-word;
    padding: 0;
    display: inline-block;
    text-align: center;
    width: 130px;
    max-height: 35px;
    line-height: 30px;
    cursor: pointer;
    border-radius: 2px;
    margin: 5px;
    font-size: 18px;

    p {
      margin-bottom: 0;
      line-height: 30px;
      display: inline-block;
    }
  }

  .pe-allow-btn-preview {
    &.pe-ant-btn {
      background-color: #2ecc71;
      color: #fff;
      border: 0;
    }
  }

  .pe-close-btn-preview {
    &.pe-ant-btn {
      background-color: #fff;
      color: #000;
      border: 0;
    }
  }
}

.pe-large-safari-preview {
  &.pe-ant-card {
    width: 390px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
    overflow: hidden;
    min-height: 200px;
  }

  .pe-ant-card-body {
    padding: 20px !important;
  }

  .pe-image-preview {
    display: block;
    margin: 0 auto;
    text-align: center;

    img {
      max-width: 80px;
      max-height: 80px;
    }
  }

  .pe-title-preview {
    word-wrap: break-word;
    max-height: 200px;
    overflow: hidden;
    text-overflow: ellipsis;

    .pe-ant-typography {
      font-size: 17px;
      font-weight: 500;
      color: #232323;
      line-height: 24px;
      margin: 0;

      p {
        margin-bottom: 0;
      }
    }
  }

  .pe-ant-btn {
    padding: 6px 8px;
    border-radius: 2px;
    font-size: 18px;
    min-width: 100px;
    line-height: 13px;

    p {
      margin-bottom: 0;
      line-height: 14px;
      display: inline-block;
    }
  }

  .pe-allow-btn-preview {
    &.pe-ant-btn {
      background-color: #2ecc71;
      color: #fff;
      border: 0;
    }
  }

  .pe-close-btn-preview {
    &.pe-ant-btn {
      background-color: #fff;
      color: #000;
      border: 0;
    }
  }

  .pe-brand-preview.pe-ant-typography {
    margin-top: 0;
  }
}

.pe-large-safari-with-segment-preview {
  .segment-preview-container {
    .segment-preview-item {
      .pe-ant-checkbox-wrapper {
        display: flex;

        .pe-ant-checkbox {
          height: 20px;
        }

        span:nth-child(2) {
          max-width: 140px;
          padding-left: 8px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }

      .pe-ant-checkbox-checked {
        &::after {
          border-color: var(--checkbox-background-color);
        }

        .pe-ant-checkbox-inner {
          background-color: var(--checkbox-background-color);
          border-color: var(--checkbox-background-color) !important;
          outline-color: var(--checkbox-background-color);

          &::after {
            border-color: var(--checkbox-tick-color);
          }
        }
      }
    }
  }
}

.pe-recovery-button-preview {
  position: absolute;
  cursor: pointer;
  display: block;
  font-size: 15px;
  line-height: 25px;
  max-width: 340px;
  padding: 6px 15px;
  border-radius: 4px 4px 0 0;

  .pe-recovery-button-close-preview {
    position: absolute;
    font-size: 13px;
    font-weight: 600;
    width: 13px;
    height: 13px;
    line-height: 11px;
    text-align: center;
    z-index: 1;
    background: #fff;
    border-radius: 50px;
    margin: 2px;
    color: #042048;
  }
}

.pe-unsubscribe-button-preview {
  position: absolute;
  bottom: 0;
  cursor: pointer;
  display: block;
  padding: 0 10px;
  border-radius: 4px 4px 0 0;
  font-size: 15px;
  line-height: 25px;
  max-width: 340px;
}

.pe-chrome-mobile-optin-preview {
  &.pe-ant-card {
    width: 90%;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
    overflow: hidden;
  }

  .pe-ant-btn {
    border: 0;
    color: #1c70e9;
    font-size: 14px;
  }
}

.pe-mask-preview {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 40px;
  background-color: rgb(0 0 0 / 30%);
}
