@import './mixins';
@import './variables';

.pe-push-campaign-form,
.pe-drip-campaign-form {
  div.pe-ant-form-item-label > label {
    font-size: 15px;
    font-weight: 600;
  }
}

.pe-ant-card.pe-campaign-card {
  margin-bottom: 4px;
  border-radius: 0;
  box-shadow: none;

  .pe-ant-card-body {
    padding: 24px 32px 28px;
    @include screen(tablet) {
      padding: 24px !important;
    }
    @include screen(mobile) {
      padding: 16px !important;
    }
  }

  &.top-left {
    border-top-left-radius: 5px;

    .pe-ant-card-body {
      padding: 32px 32px 28px;
    }
  }

  &.bottom-left {
    border-bottom-left-radius: 5px;
    margin-bottom: 0;

    .pe-ant-card-body {
      padding: 32px 32px 28px;
    }
  }

  &.left {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    margin-bottom: 0;

    .pe-ant-card-body {
      padding: 32px 32px 28px;
    }
  }

  &.right {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    margin-bottom: 0;

    .pe-ant-card-body {
      padding: 32px;
    }
  }
}

.pe-notification-large-image {
  padding: 24px;
  margin-top: 24px;
  background-color: $color-neutral-4;
  border-radius: 5px;

  .pe-large-avatar-with-close {
    position: relative;
    aspect-ratio: 2/1;
    max-width: 360px;

    .pe-empty-large-image {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 360px;
      width: 360px;
      width: min(100%, 360px);
      aspect-ratio: 2/1;
      color: $color-neutral-15;
      font-size: 47px;
      background-color: $color-neutral-10;
      border: 2px dashed $color-neutral-20;
      border-radius: 4px;
    }

    .pe-large-img-close {
      position: absolute;
      top: -8px;
      right: -8px;
    }

    img {
      max-width: 360px;
      width: 360px;
      width: min(100%, 360px);
      aspect-ratio: 2/1;
      border-radius: 4px;
    }
  }
}

.pe-notification-icon-image {
  padding: 24px;
  background-color: $color-neutral-4;
  border-radius: 5px;

  .pe-avatar-container {
    position: relative;

    img {
      width: 56px;
      height: 56px;
      border-radius: 4px;
      object-fit: cover;
    }

    .pe-empty-image {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 56px;
      height: 56px;
      border-radius: 4px;
      color: $color-neutral-15;
      background-color: $color-neutral-10;
      border: 2px dashed $color-neutral-20;
    }

    .pe-close-circle {
      position: absolute;
      top: -8px;
      right: -8px;
    }
  }
}
