@import '../../../styles/variables';
@import '../../../styles/mixins';

.pe-ant-custom-pagination {
  display: flex;

  .pe-ant-btn {
    height: 30px;
    width: 30px;
    padding: 3px 7px;
  }

  .pe-ant-pagination-item-link[disabled],
  .pe-ant-btn[disabled],
  .pe-ant-btn[disabled]:hover,
  .pe-ant-btn[disabled]:focus,
  .pe-ant-btn[disabled]:active {
    border-color: $color-neutral-15 !important;
    background-color: #f0f0f1 !important;
  }

  .pe-ant-pagination-prev,
  .pe-ant-pagination-next {
    min-width: 30px;
    margin-right: 8px;
    margin-left: 8px;
  }

  .pe-ant-pagination-item-link {
    height: 30px !important;
    width: 30px !important;
    padding: 0 7px;
    border: 1px solid $color-neutral-5 !important;
    background-color: $color-white !important;
    border-radius: 4px;

    &:not([disabled]) {
      &:hover,
      &:active {
        color: $color-primary !important;
        border-color: $color-primary !important;
        background-color: #f0f0f1;
      }
    }
  }

  .ant-pagination-slash {
    visibility: hidden;
  }

  @include screen(mobile) {
    input[type='text'] {
      min-height: 32px !important;
    }
  }

  .pe-ant-pagination-simple-pager {
    .pe-ant-pagination-slash {
      visibility: hidden;
      margin-left: 0 !important;
    }

    .pe-ant-pagination-slash::after {
      content: 'of';
      visibility: visible;
    }
  }
}
