@keyframes opt-in__switch__spin {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.opt-in__switch {
  @include reset-opt-in-style;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: nowrap;

  &.opt-in__switch--small {
    .opt-in__switch__label {
      width: 28px;
      height: 13px;

      input:checked {
        & + .opt-in__switch__slider {
          &::before {
            transform: translateX(14px);
          }
        }
      }

      .opt-in__switch__slider {
        &::before {
          height: 10px;
          width: 10px;
          bottom: 1.5px;
        }
      }
    }

    .opt-in__switch__label-title {
      font-size: 11px;
      line-height: 18px;
    }
  }

  &.opt-in__switch--spin {
    &.opt-in__switch--small {
      .opt-in__switch__label {
        input:checked {
          & + .opt-in__switch__slider::before {
            margin-left: 14px;
          }
        }
      }
    }

    .opt-in__switch__label {
      .opt-in__switch__slider::before {
        border: solid 1.5px var(--switch-checked-background-color);
        border-radius: 50%;
        border-right-color: transparent;
        border-bottom-color: transparent;
        animation: opt-in__switch__spin 1s infinite linear;
      }
    }
  }

  .opt-in__switch__label {
    margin: 0;
    padding: 0;
    position: relative;
    flex-shrink: 0;
    margin-top: 3px;

    input {
      display: none;
    }

    input:checked {
      & + .opt-in__switch__slider {
        background-color: var(--switch-checked-background-color);
      }
    }

    .opt-in__switch__slider {
      position: absolute;
      box-sizing: border-box;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: var(--switch-unchecked-background-color);
      transition: transform 0.4s;
      border-radius: 34px;

      &::before {
        box-sizing: border-box;
        display: block;
        position: absolute;
        content: '';
        height: 16px;
        width: 16px;
        left: 2px;
        bottom: 2px;
        background-color: #ffffff;
        transition: transform 0.4s;
        border-radius: 50%;
      }
    }
  }

  .opt-in__switch__label-title {
    margin-left: 6px;
    font-weight: 500;

    &.opt-in__switch__label-title--ellipsis {
      @include text-ellipsis;
    }
  }
}
