@import '../styles/variables';

#pe-flyout {
  position: fixed;
  z-index: 99999;
  transition: all 0.2s ease-in-out;
  right: 40px;
  bottom: 40px;
  opacity: 1;

  .pe-flyout-head {
    display: block;

    img {
      width: 54px;
      height: 54px;
      display: block;
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0 3px 20px rgb(0 0 0 / 20%);
      transition: all 0.2s ease-in-out;
      padding: 6px;
      position: relative;
      background-color: $color-white;
    }

    &:hover {
      img {
        box-shadow: 0 3px 30px rgb(0 0 0 / 25%);

        &:hover {
          cursor: pointer;
        }
      }

      .pe-flyout-label {
        opacity: 1;
        transform: translateY(-50%) scale(1);
        margin-right: 0;
      }
    }

    .pe-flyout-label {
      opacity: 0;
      transform: translateY(-50%) scale(0);
      margin-right: -50px;
      right: calc(100% + 15px);
    }

    &:focus {
      box-shadow: none;
    }
  }

  .pe-flyout-label {
    position: absolute;
    display: block;
    top: 50%;
    right: calc(100% + 25px);
    transform: translateY(-50%);
    color: $color-white;
    background: $color-neutral 0 0 no-repeat padding-box;
    font-size: 12px;
    white-space: nowrap;
    padding: 5px 10px;
    height: auto !important;
    line-height: initial;
    transition: all 0.2s ease-out;
    border-radius: 3px;
  }

  .pe-flyout-item {
    position: absolute;
    left: 10px;
    width: 40px;
    height: 40px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.2s ease-in-out;
    transform: scale(0);
    border-radius: 50%;
    box-shadow: 0 3px 20px rgb(0 0 0 / 20%);
    background: $color-primary-lighter 0 0 no-repeat padding-box;
    display: flex;
    align-items: center;
    justify-content: center;

    svg {
      height: 1.4em;
      width: 1.4em;
      color: $color-white;
    }

    &:hover {
      box-shadow: 0 3px 30px rgb(0 0 0 / 25%);
      background: $color-primary 0 0 no-repeat padding-box;

      .pe-flyout-label {
        background: $color-neutral 0 0 no-repeat padding-box;
      }
    }
  }

  .pe-flyout-item.pe-flyout-item-0 {
    bottom: 75px;
  }

  .pe-flyout-item.pe-flyout-item-1 {
    bottom: 130px;
  }

  .pe-flyout-item.pe-flyout-item-2 {
    bottom: 185px;
  }

  .pe-flyout-item.pe-flyout-item-3 {
    bottom: 240px;
  }
}

#pe-flyout.opened {
  .pe-flyout-item {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }

  .pe-flyout-head {
    .pe-flyout-label {
      display: none;
    }
  }

  .pe-flyout-item-0 {
    transition: transform 0.2s 0ms, background-color 0.2s;
  }

  .pe-flyout-item-1 {
    transition: transform 0.2s 35ms, background-color 0.2s;
  }

  .pe-flyout-item-2 {
    transition: transform 0.2s 70ms, background-color 0.2s;
  }

  .pe-flyout-item-3 {
    transition: transform 0.2s 105ms, background-color 0.2s;
  }
}

#pe-flyout.out {
  opacity: 0;
  visibility: hidden;
}
@media (max-width: 600px) {
  #pe-flyout {
    display: none;
  }
}
