.opt-in__popover {
  @include reset-opt-in-style;
  position: absolute;
  border-radius: 10px;
  box-shadow: $box-shadow;
  backdrop-filter: blur(2px);
  padding: 16px;
  max-width: 360px;
  min-width: 100px;
  background-color: var(--popover-background-color);
  color: var(--popover-color);
  display: none;
  flex-direction: column;
  flex-wrap: nowrap;
  visibility: hidden;

  &.opt-in__popover--opened {
    visibility: visible !important;
    display: flex;
  }

  p {
    @include reset-opt-in-paragraph-style;
  }
}
