.opt-in__bell__tooltip {
  max-width: 242px;

  .ant-tooltip-arrow {
    bottom: 16px;

    .ant-tooltip-arrow-content {
      width: 8px;
      height: 8px;
    }
  }

  .ant-tooltip-inner {
    max-height: 800px;
    overflow-y: hidden;

    .opt-in__tooltip {
      @include reset-opt-in-style;

      display: flex;
      flex-flow: column nowrap;

      p {
        @include reset-opt-in-paragraph-style;
      }
    }
  }
}
