.opt-in__large-safari-style {
  @include reset-opt-in-style;

  backdrop-filter: blur(2px);
  background-color: var(--opt-in-background-color);
  border-radius: 12px;
  box-shadow: $box-shadow;
  margin: 0;
  overflow: hidden;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  visibility: visible !important;
  display: flex;
  flex-flow: column nowrap;
  max-height: 100%;
  overflow-y: hidden;

  &.opt-in__large-safari-style--light {
    color: $default-dark-color;
  }

  &.opt-in__large-safari-style--dark {
    color: $default-light-color;
  }

  &.opt-in__large-safari-style--mobile {
    width: var(--opt-in-width);
    padding: 20px 25px 24px;

    .opt-in__large-safari-style__segments {
      margin-right: -25px;
      padding-right: 25px;
    }
  }

  &.opt-in__large-safari-style--desktop {
    width: var(--opt-in-width);
    padding: 20px 30px 24px;

    .opt-in__large-safari-style__segments {
      margin-right: -30px;
      padding-right: 30px;
    }
  }

  .opt-in__large-safari-style__content {
    align-items: center;
    display: flex;
    flex-flow: column nowrap;

    .opt-in__large-safari-style__icon__container {
      @include opt-in-icon-container;

      display: flex;
      flex-shrink: 0;

      &.opt-in__large-safari-style__icon__container--default-icon {
        @include opt-in-icon-container-default-icon;
      }

      .opt-in__large-safari-style__icon {
        @include opt-in-icon;
      }
    }

    .opt-in__large-safari-style__title {
      @include opt-in-title;

      display: flex;
      line-height: 20px;
      margin: 18px auto 0;
      max-width: calc(100% - 40px);
    }
  }

  .opt-in__large-safari-style__segments {
    @include opt-in-segment-body-scroll;

    display: flex;
    flex-wrap: wrap;
    margin-top: 18px;

    .opt-in__checkbox {
      flex-basis: calc(50% - 8px);

      &:nth-child(even) {
        margin-left: 16px;
      }
    }
  }

  .opt-in__large-safari-style__button {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    margin-top: 30px;

    .opt-in__large-safari-style__button__allow,
    .opt-in__large-safari-style__button__close {
      @include opt-in-action-button;
    }

    .opt-in__large-safari-style__button__allow {
      background: var(--allow-btn-background-color);
      color: #fff;
      margin-right: 10px;
    }

    .opt-in__large-safari-style__button__close {
      background-color: var(--close-btn-background-color);
      border: var(--close-btn-border);
      color: #677489;
    }
  }

  .opt-in__large-safari-style__powered-by {
    @include opt-in-powered-by;

    bottom: 7.5px;
    left: 50%;
    transform: translate(-50%, 0);
  }

  p {
    @include reset-opt-in-paragraph-style;
  }
}
