@import '../../styles/mixins';
@import '../../styles/variables';

.pe-audience-group-card {
  border-radius: 0;
  min-height: 120px;
  box-shadow: none !important;
}

.pe-audience-group-card-left {
  width: 100%;

  .pe-ant-card-body {
    padding: 0 25px 0 0;
  }
  @include screen(mobile) {
    .pe-ant-card-body {
      padding: 0;
    }
  }
}

.pe-audience-group-card-right {
  width: 100%;
  border-left: 1px solid $color-neutral-5;
}
@include screen(tablet) {
  .pe-audience-group-card-right {
    border-left: none;
  }
}

.audience-filter-divider {
  .pe-ant-divider-inner-text {
    background-color: #fff !important;
    padding: 0 1em !important;
  }
}
