@import '../designVariables';
@import '../designMixin';
@import '../optInBellDesign';
@import '../optInTooltip';
@import '../optInSwitch';
@import '../optInCheckbox';
@import '../optInPopover';

.management-widget {
  $opt-in-padding: 16px;

  @include reset-opt-in-style;

  visibility: visible !important;

  &.management-widget--light {
    color: $default-dark-color;
  }

  &.management-widget--dark {
    color: $default-light-color;
  }

  &.management-widget__opt-in--desktop_test_view {
    .management-widget__bell {
      margin-top: 50px;
    }
  }

  &.management-widget__opt-in--mobile_test_view {
    .management-widget__bell {
      margin-top: 50px;
      margin-bottom: 28px;

      &.management-widget__bell--right {
        right: 15px;
      }

      &.management-widget__bell--left {
        left: 15px;
      }
    }
  }

  .management-widget__bell {
    position: absolute;
    z-index: var(--z-index);
    backdrop-filter: blur(2px);
    box-shadow: 0 4px 6px rgba(121, 130, 154, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--bell-background-color);
    cursor: pointer;
    height: var(--bell-height);
    width: var(--bell-width);

    svg {
      fill: var(--bell-color);
    }

    &.management-widget__bell--default {
      svg {
        width: 19px;
        height: 19px;
      }
    }

    &.management-widget__bell--large {
      svg {
        width: 23px;
        height: 23px;
      }
    }

    &.management-widget__bell--small {
      svg {
        width: 15px;
        height: 15px;
      }
    }

    &.management-widget__bell--left {
      left: 0;
      border-radius: 0 8px 8px 0;
    }

    &.management-widget__bell--right {
      right: 0;
      border-radius: 8px 0 0 8px;
    }

    &.management-widget__bell--bottom {
      bottom: var(--bell-bottom);
    }

    &.management-widget__bell--top {
      top: var(--bell-top);
    }

    &.management-widget__bell--center {
      bottom: 50%;
      transform: translateY(calc(-50% + var(--bell-height)));
    }
  }

  .management-widget__tooltip {
    .management-widget__tooltip__title {
      @include tooltip-title;
    }
  }

  .management-widget__opt-in {
    z-index: var(--z-index);

    @media (min-width: 601px) {
      width: max-content;
    }

    &.management-widget__opt-in--center {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: max-content;
    }

    // This class is applied when the user is subscribed.
    &.management-widget__opt-in--granted {
      /*
        * The CSS class will work when both unsubscribe and auto-notification are not
        * present, and it will set the top margin of the segment block to 0.
        */
      &:not(:has(.management-widget__opt-in__unsubscribe)):not(:has(.management-widget__opt-in__auto-notification))
        .management-widget__opt-in__segment {
        margin-top: 0;
      }
    }

    // This class is applied when the user is denied.
    &.management-widget__opt-in--denied {
      .management-widget__opt-in__unsubscribe,
      .management-widget__opt-in__auto-notification {
        display: none;
      }
    }

    .management-widget__opt-in__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 24px;

      .management-widget__opt-in__title {
        @include opt-in-title;
        @include text-ellipsis;

        font-size: 14px;
        line-height: 18px;
      }

      .management-widget__opt-in__close {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 15px;

        &:hover {
          opacity: 0.7;
        }

        svg {
          width: 13px;
          height: 13px;
          fill: var(--opt-in-close-icon-color);
        }
      }
    }

    .opt-in__switch:not(:last-child) {
      margin-bottom: 10px;
    }

    .management-widget__opt-in__segment {
      @include opt-in-segment-body-scroll;

      margin-top: 8px;
      margin-right: -$opt-in-padding;
      padding-right: $opt-in-padding;
      margin-bottom: 8px;

      .management-widget__opt-in__segment__title {
        @include opt-in-title;
        @include text-ellipsis;

        font-size: 13px;
        line-height: 18px;
      }

      .management-widget__opt-in__segment__segments {
        display: flex;
        flex-wrap: wrap;
        margin-top: 14px;

        .opt-in__checkbox {
          flex-basis: calc(50% - 8px);

          &:nth-child(even) {
            margin-left: 16px;
          }
        }
      }

      .management-widget__opt-in__segment__segments > .opt-in__checkbox:only-child {
        flex-basis: 100%;
      }
    }

    .management-widget__opt-in__powered-by {
      @include opt-in-powered-by;

      bottom: 3.5px;
      right: $opt-in-padding;
    }
  }

  .management-widget__unsubscribe__popover {
    &.opt-in__popover {
      box-shadow: $box-shadow;
      z-index: var(--z-index);
    }

    .management-widget__unsubscribe__popover__title {
      @include opt-in-title;

      font-size: 12px;
      line-height: 18px;
      margin-bottom: 12px;
    }

    .management-widget__unsubscribe__popover__button {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-end;

      .management-widget__unsubscribe__popover__button_allow,
      .management-widget__unsubscribe__popover__button_close {
        @include opt-in-action-button;

        padding: 4.5px 7px;
      }

      .management-widget__unsubscribe__popover__button_allow {
        background: var(--allow-btn-background-color);
        color: var(--allow-btn-color);
        margin-right: 8px;
      }

      .management-widget__unsubscribe__popover__button_close {
        background-color: var(--close-btn-background-color);
        border: var(--close-btn-border);
        color: var(--close-btn-color);
      }
    }
  }
}
