@import '../../styles/variables';
@import '../../styles/mixins';

.pe-page-header-container {
  border-top: 4px solid $color-primary-40;

  .pe-page-header {
    height: 90px;
    padding: 33px 40px;
    @include screen(mobile) {
      padding: 25px 10px;
    }

    .pe-main-logo-nav-link {
      &:active,
      &:focus,
      &:visited {
        box-shadow: none !important;
      }
    }
  }
}

.pe-page-navigation {
  background: #fff;
  padding-left: 40px;
  padding-right: 40px;
  @include screen(tablet) {
    padding-left: 20px;
    padding-right: 20px;
  }
  @include screen(mobile) {
    padding-left: 16px;
    padding-right: 16px;
  }

  .pe-ant-tabs {
    .pe-ant-tabs-nav {
      height: 60px;
      margin-bottom: 0;

      .pe-ant-tabs-tab {
        padding-left: 18px;
        padding-right: 18px;
        margin-left: 0;

        .pe-ant-tabs-tab-btn {
          font-weight: 400;
          font-size: 15px;
          line-height: 130%;
          color: $color-neutral-60;
        }

        &.pe-ant-tabs-tab-active {
          .pe-ant-tabs-tab-btn {
            color: $color-neutral;
            font-weight: 600;
            text-shadow: none;
          }
        }
      }
    }
  }
}

.pe-help-drawer {
  .pe-ant-drawer-content {
    background: #f0f0f1;

    .pe-ant-drawer-header {
      padding: 32px 46px 32px 40px;
      border-bottom: unset;

      .pe-ant-drawer-header-title {
        flex-direction: row-reverse;
      }
    }

    .pe-ant-drawer-body {
      padding-top: 10px;
    }

    .pe-help-drawer-searched-items {
      .pe-ant-list-item {
        padding-top: 0;
        padding-bottom: 0;
        margin-bottom: 18px;
        font-size: 14px;

        &:last-child {
          margin-bottom: 42px;
        }
      }
    }

    .pe-help-drawer-collapse {
      border: unset;

      .pe-ant-collapse-header {
        height: 68px;
        padding: 24px 24px 24px 18px;
      }

      .pe-ant-collapse-content {
        background: #f0f0f1;

        .pe-ant-collapse-content-box {
          padding-bottom: 28px;
          padding-left: 20px;
          padding-top: 22px;
        }
      }

      .pe-help-drawer-list {
        .pe-ant-list-item {
          border-bottom: unset;
          padding-top: 0;
          padding-bottom: 0;
          margin-bottom: 18px;
          font-size: 14px;
        }
      }
    }
  }

  .pe-help-card {
    border-radius: 8px;
    border-color: #ddd;
    box-shadow: none;

    .pe-ant-card-body {
      padding: 44px 38px 40px;
    }

    .support-btn {
      color: #fff;
      background-color: #59a56d;
      border-color: #59a56d;

      &:hover,
      &:focus,
      &:active {
        color: #fff;
        border-color: $color-green-darker;
        background-color: $color-green-darker;
      }
    }
  }
}

.pe-notifications-alert-drawer {
  .pe-ant-drawer-header {
    padding: 18px 24px 18px 20px;
    background: #f8f8f8;

    .pe-ant-drawer-title {
      font-size: 14px;
      font-weight: 700;
      color: #454545;
    }
  }

  .pe-ant-drawer-body {
    padding: 0;

    .pe-ant-list-item {
      padding: 28px 20px 24px 22px;
      margin-bottom: 0;

      .pe-ant-list-item-meta {
        margin-bottom: 0;

        .pe-ant-list-item-meta-title {
          margin-bottom: 8px;
        }
      }
    }

    .pe-ant-list-footer {
      padding: 29px 24px 24px;

      .pe-ant-pagination {
        .pe-ant-pagination-prev,
        .pe-ant-pagination-next {
          display: none;
        }

        .pe-ant-pagination-item {
          width: 31px;
          min-width: 31px;
          height: 30px;
          background: #f8f8f8;
          border-radius: 2px;
          margin-right: 4px;
          margin-bottom: 0;
          border: unset;
          padding: 8px 10px;

          a {
            color: #454545;
            font-size: 13px;
            line-height: 14px;
            padding: 0;
          }

          &.pe-ant-pagination-item-active {
            background: #4b59d0;

            a {
              color: #fff;
            }
          }
        }
      }
    }
  }
}

.pe-header-notification-count {
  bottom: 8px;
  right: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

div.pe-header-alert {
  min-height: 45px;
  padding: 12px 16px;
  border: 1px solid #e8e8eb;
  border-radius: unset;
  background-color: #fafaff;
}
