.opt-in__bell-placed-bar__tooltip {
  .opt-in__bell-placed-bar__tooltip__title {
    @include tooltip-title;
  }

  .opt-in__bell-placed-bar__tooltip__segments {
    @include opt-in-segment-body-scroll;

    display: flex;
    flex-flow: column nowrap;
    margin-top: 10px;
    margin-right: -22px;
    padding-right: 22px;

    .pushengage-opt-in__checkbox input[type='checkbox'] {
      margin-top: 3px;
    }

    label {
      font-size: 10px;
    }
  }

  .opt-in__bell-placed-bar__tooltip__button {
    @mixin opt-in-tooltip-button-text {
      font-size: 10px;
      font-weight: 600;
      line-height: 20px;
      word-break: break-word;
      font-style: normal;
    }

    @include opt-in-tooltip-button-text;

    margin: 14px 0 0;
    cursor: pointer;
    padding: 0;
    border-radius: 0;
    text-decoration-line: underline;

    p {
      @include opt-in-tooltip-button-text;
    }
  }

  .opt-in__bell-placed-bar__tooltip__powered-by {
    @include tooltip-powered-by;
  }
}
