@import '../../../styles/variables';
@import '../../../styles/mixins';

.template-card {
  &.pe-ant-card {
    box-shadow: 1px 2px 4px rgb(0 0 0 / 5%);
  }

  .pe-ant-card-cover {
    position: relative;

    .template-img-content {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 320px;
      border-radius: 5px;
      background: linear-gradient(0deg, rgb(238 174 202 / 100%) 0%, rgb(129 141 215 / 100%) 100%);
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      padding: 0 10px;

      .notification__preview {
        transform: scale(0.902);

        .notification__preview_big-image {
          max-height: 150px;
        }
      }

      &.has-large-img {
        .notification__preview {
          transform: scale(0.85);
        }

        &.has-action-btn-img {
          .notification__preview {
            transform: scale(0.8);
          }
        }
      }
    }

    .template-hover-content {
      display: none;
    }

    .template-pro-tag {
      position: absolute;
      top: 0;
      right: 0;
      width: 40px;
      font-weight: 600;
      margin-top: 4px;
      margin-right: 4px;
      padding: 2px 1px;
      display: inline-block;
    }

    &:hover {
      background-color: #3c464d;

      .template-img-content {
        opacity: 0.4;

        .notification__preview {
          /* Preview title, message, url & button's cursor should
             be default when hover on template */
          cursor: default;

          .notification_preview_button {
            cursor: default;
          }
        }
      }

      .template-hover-content {
        display: block;
        position: absolute;
        top: 40%;
      }
    }
  }

  .pe-ant-card-body {
    padding: 18px 10px;
  }
}

.campaign-template-modal {
  .pe-ant-modal-content {
    padding: 64px !important;
    background: $color-neutral-5;
    @include screen(mobile) {
      padding: 24px;
    }

    .pe-ant-modal-body {
      padding: 0 !important;
    }
  }
}

.template-preview {
  position: fixed;
  z-index: 100000;
  inset: -1px;
  background: rgb(40 50 78 / 70%);

  .template-preview-header {
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 24px;
    padding-left: 24px;
    background: $color-neutral;
  }

  .template-preview-content {
    max-height: 100%;
    overflow: auto;
    padding-bottom: 40px;
    min-height: calc(100vh - 60px);

    .template-preview-list {
      padding: 48px 16px;
      max-width: 1280px;
      margin: auto;

      .notification__preview {
        margin: auto;
        will-change: transform;
        transition: transform 450ms;

        &:hover {
          transform: scale(1.02);
          transition: transform 125ms;
        }
      }
    }
  }
}
