@import '../../styles/variables';

.pe-ant-steps-container {
  display: flex;

  .pe-ant-step-inactive {
    display: flex;
    align-self: center;

    .pe-ant-step {
      display: inline-block;
      width: 16px;
      height: 16px;
      background: #c9d0d6;
      border-radius: 100px;
    }
  }

  .pe-ant-step-active {
    display: flex;
    align-self: center;

    .pe-ant-step {
      display: inline-block;
      width: 16px;
      height: 16px;
      background: #dedee5;
      border: 3px solid $color-primary-40;
      border-radius: 100px;
    }
  }

  .pe-step-complete {
    display: flex;
    align-self: center;

    .pe-ant-step {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      background: $color-primary-40;
      border-radius: 100px;
    }
  }

  .pe-ant-step-divider {
    display: flex;
    align-self: center;
    justify-content: center;
    width: 100%;
    max-width: 80px;
    height: 1.5px;
    margin-right: 4px;
    margin-left: 4px;
    background: #c9d0d6;
  }

  .pe-ant-step-divider-active {
    background: $color-primary-40;
  }
}

.pe-onboarding-steps {
  .pe-ant-steps-container {
    justify-content: center;

    .pe-ant-step-active .pe-ant-step {
      background: $color-primary-40;
      border: 1.5px solid $color-primary-40;
    }

    .pe-ant-step-divider {
      margin-left: 0;
      margin-right: 0;
    }
  }
}
