.demographic-chart-card {
  height: 100%;

  .demographic-list {
    max-width: 200px;
    max-height: 145px;
    padding-right: 16px;
    overflow: auto;

    .demographic-list-item {
      margin-bottom: 4px;

      .pe-ant-badge-status-dot {
        height: 10px;
        width: 10px;
      }
    }
  }

  .pe-ant-card-head {
    border-bottom: none !important;
    padding: 0 24px;

    .pe-ant-card-head-title {
      font-size: 16px;
      font-weight: 700;
      padding: 24px 0 0;
    }
  }

  .pe-ant-card-body {
    padding: 24px;
  }
}
