@import '../../../styles/variables';

.pe-app-filter-card {
  border-color: $color-neutral-15;
}

.pe-source-checkbox-group {
  display: flex;
  flex-wrap: wrap;
  gap: 8px 8px;

  label.pe-ant-checkbox-wrapper > span {
    font-size: 13px;
    font-weight: 400;
    color: $color-neutral-60;
  }

  .pe-ant-checkbox + span {
    padding-left: 9px;
    margin-right: 0;
  }
}
