@import './../../styles/variables';
@import './../../styles/mixins';

.pe-report-card-container {
  .pe-analytics-collapse-panel {
    margin-bottom: 16px;
    overflow: hidden;
    background: #fff;
    border: 0;
    border-radius: 4px !important;

    .pe-ant-collapse-header {
      padding-top: 22px;
      padding-bottom: 19px;

      .pe-ant-collapse-expand-icon {
        .pe-ant-collapse-arrow {
          display: flex;
          width: 30px;
          height: 30px;
          border: 1px solid #c9c9d6;
          border-radius: 50%;
          justify-content: center;
          align-items: center;
        }
      }
    }

    .pe-ant-collapse-content {
      .pe-ant-collapse-content-box {
        margin-top: 0;
        padding-top: 0;
        padding-right: 0;
        padding-bottom: 30px;
      }
    }

    .analytics-report-card {
      border-radius: 5px;
      box-shadow: unset;
      border: unset;

      .pe-ant-card-head {
        padding-left: 0;
        padding-right: 0;

        .pe-ant-card-head-title {
          padding-top: 30px;
          padding-bottom: 30px;
        }
      }

      .pe-ant-card-body {
        padding-top: 46px;
        padding-left: 0;
        padding-bottom: 19px;
      }

      &.ant-card-bordered {
        border: 2px solid $color-neutral-15;
      }

      .ant-card-head {
        border-bottom: 1px solid $color-neutral-15;

        &-title {
          padding: 8px 0;
        }
      }

      .ant-card-body {
        padding: 0;

        .second-part {
          align-self: center;
          padding: 48px 24px 24px;

          .flexbox {
            display: flex;

            .flexbox-item {
              margin-right: 60px;
            }
          }

          @include screen(mobile) {
            .flexbox {
              flex-direction: column;
            }
          }
        }

        .first-part {
          padding: 24px 48px;
          background: $color-neutral-5;
          border-radius: 0 0 5px;
          margin-top: 1px;
        }
      }
    }
  }
}

.pe-goal-tracking-page {
  .pe-ant-card-head-title {
    padding-top: 22px;
    padding-bottom: 19px;
  }

  .pe-ant-card-body {
    padding: 30px 26px 40px;

    .pe-enable-goal-tracking-form-item {
      .pe-ant-form-item-label {
        display: flex;
        align-items: center;
      }
    }
  }
}
